<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 메인 배너 관리 </span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" v-b-modal.modal-1> 추가 </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table hover :items="bannerMainList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
          <template #cell(BM_path_img2)="row">
            <b-img id="admin-user-popup-img" :src="row.item.BM_path_img2" />
          </template>

          <template #cell(수정)="row">
            <b-button size="sm" v-b-modal.modal-2 variant="primary" @click="editModalBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
          </template>
          <template #cell(삭제)="row">
            <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
      <b-modal id="modal-1" title="메인 팝업 추가" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="saveBannerMain">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th style="width: 25%">순서</b-th>
            <b-td>
              <b-form-input v-model="BM_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td>
              <b-form-input v-model="BM_title" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>유형</b-th>
            <b-td>
              <b-form-select v-model="BM_category" :options="options"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr v-if="BM_category == 'URL'">
            <b-th>URL</b-th>
            <b-td>
              <b-form-input v-model="BM_url" />
            </b-td>
          </b-tr>
          <b-tr v-if="BM_category == '이미지'">
            <b-th>이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile1($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr v-if="BM_category == '공지'">
            <b-th>공지내용</b-th>
            <b-td>
              <b-form-textarea id="textarea" v-model="BM_notice" rows="3"></b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr v-if="BM_category == '공지'">
            <b-th>공지이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile2($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>배너이미지<br /><span id="img-set-font">*16:9 size</span></b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
      <b-modal
        id="modal-2"
        title="메인 팝업 수정"
        cancel-title="닫기"
        cancel-variant="danger"
        ok-title="수정"
        @ok="editBanner"
        @cancel="cancelModal"
      >
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th style="width: 30%">순서</b-th>
            <b-td colspan="2">
              <b-form-input v-model="bannerRowMainList.BM_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td colspan="2">
              <b-form-input v-model="bannerRowMainList.BM_title" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>유형</b-th>
            <b-td colspan="2">
              <b-form-select v-model="bannerRowMainList.BM_category" :options="options"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr v-if="bannerRowMainList.BM_category == 'URL'">
            <b-th>URL</b-th>
            <b-td colspan="2">
              <b-form-input v-model="bannerRowMainList.BM_url" />
            </b-td>
          </b-tr>
          <b-tr v-if="bannerRowMainList.BM_category == '이미지'">
            <b-th>이미지</b-th>
            <b-td style="width: 55%">
              <input type="file" accept="image/*" @change="uploadEditFile1($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td v-if="bannerRowMainList.BM_img_path_img != ''">
              <a :href="bannerRowMainList.BM_img_path_img2" onclick="window.open(this.href, '_blank', 'width=100%'); return false;">
                미리 보기
              </a>
            </b-td>
          </b-tr>
          <b-tr v-if="bannerRowMainList.BM_category == '공지'">
            <b-th>공지내용</b-th>
            <b-td colspan="2">
              <b-form-textarea id="textarea" v-model="bannerRowMainList.BM_notice" rows="3"></b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr v-if="bannerRowMainList.BM_category == '공지'">
            <b-th>공지이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadEditFile2($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td v-if="bannerRowMainList.BM_notice_path_img != ''">
              <a :href="bannerRowMainList.BM_notice_path_img2" onclick="window.open(this.href, '_blank', 'width=100%'); return false;">
                미리 보기
              </a>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>배너이미지<br /><span id="img-set-font">*16:9 size</span></b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadEditFile($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td>
              <a :href="bannerRowMainList.BM_path_img2" onclick="window.open(this.href, '_blank', 'width=100%'); return false;">
                미리 보기
              </a>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      BM_order: "",
      BM_title: "",
      BM_category: "URL",
      BM_notice: "",
      BM_url: "",
      BM_path_img: "",
      BM_path_img_edit: "",
      BM_notice_path_img: "",
      BM_notice_path_img_edit: "",
      BM_img_path_img: "",
      BM_img_path_img_edit: "",
      bannerMainList: [],
      bannerRowMainList: [],
      fields: [
        { key: "BM_order", label: "순서", thClass: "w10" },
        { key: "BM_title", label: "제목", thClass: "w30" },
        { key: "BM_category", label: "유형", thClass: "w20" },
        { key: "BM_path_img2", label: "썸네일", thClass: "w20" },
        "수정",
        "삭제",
      ],
      options: [
        { value: "URL", text: "URL" },
        { value: "이미지", text: "이미지" },
        { value: "공지", text: "공지" },
      ],
      perPage: 6,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getBannerMainList();
  },
  computed: {
    rows() {
      return this.bannerMainList.length;
    },
  },
  watch: {
    BM_category(value) {
      if (value == "URL") {
        this.BM_notice = "";
        this.BM_notice_path_img = "";
        this.BM_img_path_img = "";
      } else if (value == "공지") {
        this.BM_url = "";
        this.BM_img_path_img = "";
      } else {
        this.BM_url = "";
        this.BM_notice = "";
      }
    },
  },
  methods: {
    uploadFile(e) {
      this.BM_path_img = e.target.files[0];
    },
    uploadEditFile(e) {
      this.BM_path_img_edit = e.target.files[0];
    },
    uploadFile1(e) {
      this.BM_img_path_img = e.target.files[0];
    },
    uploadEditFile1(e) {
      this.BM_img_path_img_edit = e.target.files[0];
    },
    uploadFile2(e) {
      this.BM_notice_path_img = e.target.files[0];
    },
    uploadEditFile2(e) {
      this.BM_notice_path_img_edit = e.target.files[0];
    },
    editModalBtn(item) {
      this.bannerRowMainList = { ...item };
    },
    cancelModal() {
      this.bannerRowMainList = [];
    },
    async getBannerMainList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/mainbanner/list`);
        this.bannerMainList = response.data;
        for (var i = 0; i < this.bannerMainList.length; i++) {
          this.bannerMainList[i].BM_path_img2 = `${this.$store.state.host}/${response.data[i].BM_path_img}`;
          this.bannerMainList[i].BM_img_path_img2 = `${this.$store.state.host}/${response.data[i].BM_img_path_img}`;
          this.bannerMainList[i].BM_notice_path_img2 = `${this.$store.state.host}/${response.data[i].BM_notice_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },

    async saveBannerMain(bvModalEvent) {
      if (this.BM_path_img == "") {
        this.$swal("어랏!", "배너 이미지를 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        if (this.BM_category == "이미지" && this.BM_img_path_img == "") {
          this.$swal("어랏!", "이미지를 입력해주세요", "question");
          bvModalEvent.preventDefault();
        } else {
          const fd = new FormData();
          fd.append("BM_order", this.BM_order);
          fd.append("BM_title", this.BM_title);
          fd.append("BM_category", this.BM_category);
          fd.append("BM_path_img", this.BM_path_img);

          if (this.BM_category == "URL") {
            fd.append("BM_url", this.BM_url);
          }
          if (this.BM_category == "이미지") {
            fd.append("BM_img_path_img", this.BM_img_path_img);
          }
          if (this.BM_category == "공지") {
            fd.append("BM_notice", this.BM_notice);
            fd.append("BM_notice_path_img", this.BM_notice_path_img);
          }
          await this.$axios.post(`${this.$store.state.host}/admin/user/mainbanner/create`, fd);
          this.$swal("Success", "저장 되었습니다.", "success");
          this.BM_order = "";
          this.BM_title = "";
          this.BM_category = "";
          this.BM_url = "URL";
          this.BM_notice = "";
          this.BM_notice_path_img = "";
          this.BM_img_path_img = "";
          this.BM_path_img = "";
          this.getBannerMainList();
        }
      }
    },

    async editBanner() {
      try {
        const fd = new FormData();
        fd.append("BM_order", this.bannerRowMainList.BM_order);
        fd.append("BM_title", this.bannerRowMainList.BM_title);
        fd.append("BM_category", this.bannerRowMainList.BM_category);

        if (this.bannerRowMainList.BM_category == "URL") {
          fd.append("BM_url", this.bannerRowMainList.BM_url);
          fd.append("BM_path_img", this.bannerRowMainList.BM_path_img);
          fd.append("BM_path_img_edit", this.BM_path_img_edit);

          fd.append("BM_img_path_img", "");

          fd.append("BM_notice", "");
          fd.append("BM_notice_path_img", "");
        }
        if (this.bannerRowMainList.BM_category == "이미지") {
          fd.append("BM_url", "");
          fd.append("BM_path_img", this.bannerRowMainList.BM_path_img);
          fd.append("BM_path_img_edit", this.BM_path_img_edit);

          fd.append("BM_img_path_img", this.bannerRowMainList.BM_img_path_img);
          fd.append("BM_img_path_img_edit", this.BM_img_path_img_edit);

          fd.append("BM_notice", "");
          fd.append("BM_notice_path_img", "");
        }
        if (this.bannerRowMainList.BM_category == "공지") {
          fd.append("BM_url", "");
          fd.append("BM_path_img", this.bannerRowMainList.BM_path_img);
          fd.append("BM_path_img_edit", this.BM_path_img_edit);

          fd.append("BM_img_path_img", "");

          fd.append("BM_notice", this.bannerRowMainList.BM_notice);
          fd.append("BM_notice_path_img", this.bannerRowMainList.BM_notice_path_img);
          fd.append("BM_notice_path_img_edit", this.BM_notice_path_img_edit);
        }

        await this.$axios.post(`${this.$store.state.host}/admin/user/mainbanner/edit/${this.bannerRowMainList.BM_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.BM_path_img_edit = "";
        this.BM_img_path_img_edit = "";
        this.BM_notice_path_img_edit = "";
        this.getBannerMainList();
      } catch (err) {
        console.log(err);
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/bannermain/delete/${item.BM_idx}`, {
              BM_path_img: item.BM_path_img,
              BM_img_path_img: item.BM_img_path_img,
              BM_notice_path_img: item.BM_notice_path_img,
            });
            this.getBannerMainList();
            this.$toast.success("삭제되었습니다");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
