<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 공지사항(고객) </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 7%">순서</b-th>
            <b-th style="width: 30%">제목</b-th>
            <b-th style="width: 33%">내용</b-th>
            <b-th style="width: 20%">이미지파일</b-th>
            <b-th style="width: 10%">저장</b-th>
          </b-tr>
          <b-td>
            <b-form-input v-model="BN_order" onKeyup="this.value=this.value.replace(/[^0-9]/g,'');" />
          </b-td>
          <b-td>
            <b-form-input v-model="BN_title" />
          </b-td>
          <b-td>
            <b-form-textarea id="textarea" v-model="BN_content" rows="5" max-rows="6"></b-form-textarea>
          </b-td>
          <b-td>
            <input type="file" accept="image/*" @change="uploadFile($event)" id="admin-store-file" />
          </b-td>
          <b-td>
            <b-button variant="warning" style="font-weight: bold" @click="saveNotice"> 저장 </b-button>
          </b-td>
        </b-table-simple>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table hover :items="boardNotice" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
          <template #cell(BN_idx)="data">
            <b id="mypage-orderNumer-fontcolor">{{ data.value }}</b>
          </template>
          <template #cell(BN_order)="row">
            <b-form-input v-model="row.item.BN_order" />
          </template>
          <template #cell(BN_title)="row">
            <b-form-input v-model="row.item.BN_title" />
          </template>
          <template #cell(BN_content)="row">
            <b-form-textarea id="textarea" v-model="row.item.BN_content" rows="3" max-rows="6"></b-form-textarea>
          </template>
          <template #cell(이미지파일)>
            <input type="file" accept="image/*" @change="uploadEditFile($event)" id="admin-store-file" />
          </template>
          <template #cell(BN_path_img)="row">
            <b-img v-if="row.item.BN_path_img != ''" id="admin-board-img" :src="row.item.BN_path_img2" />
          </template>

          <template #cell(수정)="row">
            <b-button size="sm" variant="warning" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
          </template>
          <template #cell(삭제)="row">
            <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      BN_order: "",
      BN_title: "",
      BN_content: "",
      BN_path_img: "",
      BN_path_img_edit: "",
      boardNotice: [],
      fields: [
        { key: "BN_idx", label: "번호", thClass: "w5" },
        { key: "BN_order", label: "순서", thClass: "w5" },
        { key: "BN_title", label: "제목", thClass: "w20" },
        { key: "BN_content", label: "내용", thClass: "w30" },
        "이미지파일",
        { key: "BN_path_img", label: "미리보기", thClass: "w10" },
        "수정",
        "삭제",
      ],
      perPage: 4,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getBoardNoticeList();
  },
  computed: {
    rows() {
      return this.boardNotice.length;
    },
  },
  methods: {
    uploadFile(e) {
      this.BN_path_img = e.target.files[0];
    },
    uploadEditFile(e) {
      this.BN_path_img_edit = e.target.files[0];
    },
    async getBoardNoticeList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/notice/user`);
        this.boardNotice = response.data;
        for (var i = 0; i < this.boardNotice.length; i++) {
          this.boardNotice[i].BN_path_img2 = `${this.$store.state.host}/${response.data[i].BN_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async saveNotice() {
      if (this.BN_title == "") {
        this.$swal("어랏!", "제목을 입력해주세요", "question");
        return;
      } else if (this.BN_content == "") {
        this.$swal("어랏!", "내용을 입력해주세요", "question");
        return;
      } else {
        const fd = new FormData();
        fd.append("BN_user", "USER");
        fd.append("BN_order", this.BN_order);
        fd.append("BN_title", this.BN_title);
        fd.append("BN_content", this.BN_content);
        fd.append("BN_path_img", this.BN_path_img);

        await this.$axios.post(`${this.$store.state.host}/admin/user/notice/save`, fd);
        this.$toast.success("저장 되었습니다.");
        this.BN_order = "";
        this.BN_title = "";
        this.BN_content = "";
        this.BN_path_img = "";
        this.getBoardNoticeList();
      }
    },

    async editBtn(item) {
      try {
        const fd = new FormData();
        fd.append("BN_order", item.BN_order);
        fd.append("BN_title", item.BN_title);
        fd.append("BN_content", item.BN_content);
        fd.append("BN_path_img", item.BN_path_img);
        fd.append("BN_path_img_edit", this.BN_path_img_edit);

        await this.$axios.post(`${this.$store.state.host}/admin/user/notice/edit/${item.BN_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.BN_path_img_edit = "";
        this.getBoardNoticeList();
      } catch (err) {
        console.log(err);
        this.$toast.error("실패했습니다.");
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/notice/delete/${item.BN_idx}`, { BN_path_img: item.BN_path_img });
            this.getBoardNoticeList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
