<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 회원(판매사)</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn1" v-b-modal.modal-1> 푸시발송 </b-button>
          <b-button id="admin-store-member-topBtn2" @click="goPauseList"> 이용정지내역 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput">
          <div class="mr-1">
            <b-form-select v-model="selectedUsed" :options="options1"></b-form-select>
          </div>
          <div class="mr-1">
            <b-form-select v-model="selectedID" :options="options2"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getStoreSearchMemberList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getStoreSearchMemberList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="storeMemberList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(SM_tel)="row">
              {{ row.item.SM_tel | phoneFomatter }}
            </template>
            <template #cell(SM_isShow)="row">
              <div v-if="row.item.SM_isShow == 'USE'">이용중</div>
              <div v-if="row.item.SM_isShow == 'DISABLE'">비활성화</div>
              <div v-if="row.item.SM_isShow == 'STOP'">이용정지</div>
            </template>
            <template #cell(SM_memo)="row">
              <b-form-textarea id="textarea" v-model="row.item.SM_memo" rows="2" max-rows="4"></b-form-textarea>
            </template>
            <template #cell(수정)="row">
              <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
            </template>
            <template #cell(이용정지)="row">
              <div v-if="row.item.SM_isShow == 'USE' || row.item.SM_isShow == 'DISABLE'">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="usedBtn(row.item)" id="board_edit_btn"> 이용정지 </b-button>
              </div>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>

      <!-- 모달 부분 -->
      <b-modal id="modal-1" title="개별 푸시 발송" cancel-title="닫기" cancel-variant="danger" ok-title="발송" @ok="goPush">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-td class="text-center font-weight-bold" colspan="2"> 메세지를 입력해주세요 </b-td>
          </b-tr>
          <b-tr>
            <b-th>ID</b-th>
            <b-td>
              <b-input-group>
                <b-form-input size="sm" v-model="modalPushId" />
                <b-input-group-append>
                  <b-button size="sm" variant="warning" @click="pushCheckId">
                    <i class="bi bi-search"></i>
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td><b-form-input size="sm" v-model="modalPushTitle" /></b-td>
          </b-tr>
          <b-tr>
            <b-th>내용</b-th>
            <b-td>
              <b-form-textarea id="textarea" rows="4" max-rows="6" v-model="modalPushContent"> </b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile($event)" id="admin-store-file-modal" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>

      <b-modal id="modal-2" title="이용중지(판매사)" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="goPause">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th>ID</b-th>
            <b-td>
              <b-form-input size="sm" v-model="modalPauseId" readonly />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>정지기간</b-th>
            <b-td>
              <b-form-select v-model="modalPauseDuration" size="sm" :options="options3"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>정지사유</b-th>
            <b-td>
              <b-form-input size="sm" v-model="modalPauseReason" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>메모</b-th>
            <b-td>
              <b-form-textarea id="textarea" rows="3" max-rows="4" v-model="modalPauseMemo"> </b-form-textarea>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>
<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      storeMemberList: [],
      fields: [
        { key: "SM_m_id", label: "몽키컨설팅계정", thClass: "w20" },
        {
          key: "SM_store_name",
          label: "매장명",
          thClass: "w15",
        },
        {
          key: "SM_tel",
          label: "매장 연락처",
          thClass: "w10",
        },

        { key: "SM_isShow", label: "상태", thClass: "w10" },
        { key: "SM_memo", label: "메모", thClass: "w20" },
        "수정",
        "이용정지",
      ],
      options1: [
        { value: "USE", text: "이용중" },
        { value: "DISABLE", text: "비활성화" },
        { value: "STOP", text: "이용정지" },
      ],
      options2: [
        { value: "계정", text: "계정" },
        { value: "매장명", text: "매장명" },
      ],
      options3: [
        { value: 1, text: "1일" },
        { value: 3, text: "3일" },
        { value: 7, text: "7일" },
        { value: 30, text: "30일" },
        { value: "무제한", text: "무제한" },
      ],
      selectedUsed: "USE",
      selectedID: "계정",
      keyword: "",
      perPage: 8,
      filter: null,
      currentPage: 1,
      idChecked: false,
      modalPushId: "",
      modalPushTitle: "",
      modalPushContent: "",
      modalPushPathImg: "",
      modalPauseId: "",
      modalPauseDuration: "",
      modalPauseReason: "",
      modalPauseMemo: "",
      token: "",
      modalPauseIdShow: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getStoreMemberList();
  },
  computed: {
    rows() {
      return this.storeMemberList.length;
    },
  },
  methods: {
    uploadFile(e) {
      this.modalPushPathImg = e.target.files[0];
    },
    async getStoreMemberList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/member/list`);
        this.storeMemberList = response.data;
        this.isLoding = true;
      } catch (err) {
        this.$swal("Error", "실패했습니다.", "error");
        console.log(err);
      }
    },
    async getStoreSearchMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/search`, {
          selectedUsed: this.selectedUsed,
          selectedID: this.selectedID,
          keyword: this.keyword,
        });
        this.storeMemberList = response.data;
        this.isLoding = true;
      } catch (err) {
        this.$swal("Error", "실패했습니다.", "error");
        console.log(err);
      }
    },

    async editBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/edit/${item.SM_idx}`, {
          SM_memo: item.SM_memo,
        });
        if (response.status == 200) {
          this.$swal("Success", "수정되었습니다.", "success");
          this.isLoding = false;
          this.getStoreMemberList();
          this.isLoding = true;
        }
      } catch (err) {
        this.$swal("Error", "실패했습니다.", "error");
        console.log(err);
      }
    },
    async pushCheckId() {
      this.idChecked = false;
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/member/find/${this.modalPushId}`);
        if (response.data != "") {
          this.$swal("Success", "해당 아이디 매칭완료!", "success");
          this.token = response.data.SM_token;
          this.idChecked = true;
        } else {
          this.$swal("Error", "아이디를 다시 확인 해주세요.", "error");
        }
      } catch (err) {
        console.log(err);
      }
    },
    usedBtn(item) {
      this.modalPauseId = item.SM_m_id;
      this.modalPauseIdShow = item.SM_isShow;
    },
    async goPush(bvModalEvent) {
      if (this.idChecked == false || this.modalPushId == "") {
        this.$swal("어랏!", "아이디 확인 부탁드립니다", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPushTitle == "") {
        this.$swal("어랏!", "제목을 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPushContent == "") {
        this.$swal("어랏!", "내용을 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          const fd = new FormData();
          fd.append("title", this.modalPushTitle);
          fd.append("msg", this.modalPushContent);
          fd.append("img", this.modalPushPathImg);
          fd.append("token", this.token);

          const response = await this.$axios.post(`${this.$store.state.host}/admin/store/push`, fd);

          if (response.status == 200) {
            this.$swal("Success", "푸시를 성공적으로 보냈습니다!", "success");
            this.modalPushId = "";
            this.modalPushTitle = "";
            this.modalPushContent = "";
            this.idChecked = false;
            this.token = "";
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    async goPause(bvModalEvent) {
      if (this.modalPauseDuration == "") {
        this.$swal("어랏!", "정지 기간을 선택해주세요", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPauseReason == "") {
        this.$swal("어랏!", "정지 사유를 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          let show = "STOP_USE";
          if (this.modalPauseIdShow == "DISABLE") {
            show = "STOP_DISABLE";
          }
          const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/pause/${this.modalPauseId}`, {
            SM_pause_duration: this.modalPauseDuration,
            SM_pause_reason: this.modalPauseReason,
            SM_pause_memo: this.modalPauseMemo,
            SM_isShow: show,
          });
          if (response.status == 200) {
            this.$swal("Success", "저장되었습니다", "success");
            this.isLoding = false;
            this.getStoreMemberList();
            this.isLoding = true;
            this.modalPauseDuration = "";
            this.modalPauseReason = "";
            this.modalPauseMemo = "";
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    goPauseList() {
      this.$router.push("/admin/store/member/pauselist").catch(() => {});
    },
  },
};
</script>

<style></style>
