import VueCookies from "vue-cookies";
import axios from "axios";
const userStore = {
  namespaced: true,
  state: {
    AM_idx: "",
    AM_name: "",
    AM_rank: "",
  },
  mutations: {
    login: function (state, data) {
      state.AM_idx = data.AM_idx;
      state.AM_name = data.AM_name;
      state.AM_rank = data.AM_rank;
    },

    logout: function (state) {
      state.AM_idx = "";
      state.AM_name = "";
      state.AM_rank = "";
    },
  },
  actions: {
    getAuthMemberinfo: async function ({ commit }) {
      let token = VueCookies.get("token_banana_admin");
      axios.defaults.headers.common["token"] = token;
      await axios
        .post(`http://imsi.bananadeal.co.kr:3000/admin/login/refreshtoken`)
        .then((res) => {
          commit("login", res.data);
        })
        .catch((err) => {
          return err;
        });
    },
  },
};

export default userStore;
