<template>
  <div>
    <div id="admin-main-box" class="container-fluid">
      <div>
        <span v-if="duration == 'DAY'" id="dashboard-main-title"> 대시보드 {{ dashboardDate }} 기준</span>
        <span v-else-if="duration == 'ALL'" id="dashboard-main-title"> 대시보드 전체 기준</span>
        <span v-else id="dashboard-main-title"> 대시보드 {{ dashboardDate2 | yyyyMMdd }} ~ {{ dashboardDate }} 기준</span>
        <!-- <span style="margin-left: 40vw"> -->
        <span id="dashboard-top-title-btn">
          <b-button size="sm" id="admin-dashborad-btn" variant="warning" @click="period('DAY')">일간</b-button>
          <b-button size="sm" id="admin-dashborad-btn" variant="warning" @click="period('WEEK')">주간</b-button>
          <b-button size="sm" id="admin-dashborad-btn" variant="warning" @click="period('MONTH')">월간</b-button>
          <b-button size="sm" id="admin-dashborad-btn" variant="warning" @click="period('ALL')">전체</b-button>
        </span>
      </div>
      <div id="admin-dashboard-borad-top2">
        <div id="admin-board-title">사용자 통계(고객)</div>
        <span id="admin-dashboard-borad-deal1">
          <table id="admin-dashboard-table">
            <tr>
              <td>
                <apexChart v-show="duration == 'DAY'" type="area" height="280" :options="chartOptions" :series="series"></apexChart>
                <apexChart v-show="duration == 'WEEK'" type="area" height="280" :options="chartOptions" :series="series"></apexChart>
                <apexChart v-show="duration == 'MONTH'" type="area" height="280" :options="chartOptions" :series="series"></apexChart>
                <apexChart v-show="duration == 'ALL'" type="area" height="280" :options="chartOptions" :series="series"></apexChart>
              </td>
            </tr>
          </table>
        </span>
        <span>
          <table id="admin-dashboard-table2">
            <tr>
              <td id="admin-td-color">다운로드수</td>
              <td>{{ downCnt }}</td>
            </tr>
            <tr>
              <td id="admin-td-color">가입자수</td>
              <td>{{ joinCnt }}</td>
            </tr>
            <tr>
              <td id="admin-td-color">탈퇴수</td>
              <td>{{ outCnt }}</td>
            </tr>
          </table>
        </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <div id="admin-board-title">딜현황</div>
        <span id="admin-dashboard-borad-deal1">
          <table id="admin-dashboard-table">
            <tr>
              <td id="admin-td-weight">요청건수</td>
              <td>
                <b-progress :value="deal1" variant="success" :max="max"></b-progress>
              </td>
            </tr>
            <tr>
              <td id="admin-td-weight">참여건수</td>
              <td>
                <b-progress :value="deal2" variant="warning" :max="max"></b-progress>
              </td>
            </tr>
            <tr>
              <td id="admin-td-weight">수락건수</td>
              <td>
                <b-progress :value="deal3" variant="danger" :max="max"></b-progress>
              </td>
            </tr>
            <tr>
              <td id="admin-td-weight">개통건수</td>
              <td>
                <b-progress :value="deal4" variant="info" :max="max"></b-progress>
              </td>
            </tr>
          </table>
        </span>
        <span>
          <table id="admin-dashboard-table2">
            <tr>
              <td id="admin-td-color">요청건수</td>
              <td>{{ deal1 }}</td>
            </tr>
            <tr>
              <td id="admin-td-color">참여건수</td>
              <td>{{ deal2 }}</td>
            </tr>
            <tr>
              <td id="admin-td-color">수락건수</td>
              <td>{{ deal3 }}</td>
            </tr>
            <tr>
              <td id="admin-td-color">개통건수</td>
              <td>{{ deal4 }}</td>
            </tr>
          </table>
        </span>
      </div>

      <Inquiry />
      <Report />
    </div>
  </div>
</template>

<script>
import Inquiry from "@/components/Admin_home/Admin_inquiry.vue";
import Report from "@/components/Admin_home/Admin_report.vue";

export default {
  components: {
    Inquiry: Inquiry,
    Report: Report,
  },
  data() {
    return {
      dashboardDate: this.$store.state.today2,
      dashboardDate2: "",
      duration: "DAY",
      deal1: 0,
      deal2: 0,
      deal3: 0,
      deal4: 0,
      max: 100,
      series: [
        {
          name: "다운로드수",
          data: [],
        },
        {
          name: "가입자수",
          data: [],
        },
        {
          name: "탈퇴수",
          data: [],
        },
      ],
      chartOptions: {
        chart: {
          type: "area",
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        xaxis: {
          categories: [],
        },
        tooltip: {
          x: {
            format: "dd/MM/yy",
          },
        },
      },
      UserReportList: [],
      bananaData: [],
      downCnt: 0,
      joinCnt: 0,
      outCnt: 0,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        alert("로그인 후 이용 바랍니다.");
        this.$router.push("/");
      }
    });
    this.getDealData(),
      this.getBananaData().then(() => {
        this.duration = "WEEK";
        let today = new Date();
        this.dashboardDate2 = new Date(today.getFullYear(), today.getMonth(), today.getDate() - 30);
      });
  },
  methods: {
    period(day) {
      this.duration = day;
      if (day == "DAY") {
        this.dashboardDate = this.$store.state.today2;
      }
      if (day == "WEEK") {
        let today = new Date();
        this.dashboardDate2 = new Date(today.getFullYear(), today.getMonth(), today.getDate() - 6);
      }
      if (day == "MONTH") {
        let today = new Date();
        this.dashboardDate2 = new Date(today.getFullYear(), today.getMonth(), today.getDate() - 30);
      }
    },
    async getDealData() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/home/dashboard/deal/${this.duration}`);
        this.deal1 = 0;
        this.deal2 = 0;
        this.deal3 = 0;
        this.deal4 = 0;
        this.max = parseInt(response.data.req_cnt) + 10;
        this.deal1 = response.data.req_cnt;
        this.deal2 = response.data.PARTICIPATE_cnt;
        this.deal3 = response.data.ACCEPT_cnt;
        this.deal4 = response.data.OPEN_cnt;
      } catch (err) {
        console.log(err);
      }
    },
    async getBananaData() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/home/dashboard/banana/${this.duration}`);
        this.downCnt = 0;
        this.joinCnt = 0;
        this.outCnt = 0;
        this.series[0].data = [];
        this.series[1].data = [];
        this.series[2].data = [];
        this.bananaData = response.data;

        if (this.duration == "ALL") {
          this.bananaData.splice(0, 19);
        }

        for (var i = 0; i < this.bananaData.length; i++) {
          this.downCnt += parseInt(this.bananaData[i].down_count);
          this.joinCnt += this.bananaData[i].join_count;
          this.outCnt += this.bananaData[i].out_count;
          if (this.duration == "ALL") {
            this.series[0].data.push({
              x: this.bananaData[i].download_date,
              y: this.bananaData[i].down_count,
            });
            this.series[1].data.push({
              x: this.bananaData[i].download_date,
              y: this.bananaData[i].join_count,
            });
            this.series[2].data.push({
              x: this.bananaData[i].download_date,
              y: this.bananaData[i].out_count,
            });
          } else {
            this.series[0].data.push({
              x: this.yyyyMMdd(this.bananaData[i].download_date),
              y: this.bananaData[i].down_count,
            });
            this.series[1].data.push({
              x: this.yyyyMMdd(this.bananaData[i].download_date),
              y: this.bananaData[i].join_count,
            });
            this.series[2].data.push({
              x: this.yyyyMMdd(this.bananaData[i].download_date),
              y: this.bananaData[i].out_count,
            });
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
    yyyyMMdd(value) {
      if (value == "") return "";
      var js_date = new Date(value);
      var year = js_date.getFullYear();
      var month = js_date.getMonth() + 1;
      var day = js_date.getDate();

      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      return year + "-" + month + "-" + day;
    },
  },

  watch: {
    duration() {
      this.getBananaData();
      this.getDealData();
    },
  },
};
</script>

<style></style>
