<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 푸시발송 </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 5%">번호</b-th>
            <b-th style="width: 5%">발송수</b-th>
            <b-th style="width: 25%">제목</b-th>
            <b-th style="width: 25%">메세지</b-th>
            <b-th style="width: 20%">이미지파일</b-th>
            <b-th style="width: 10%">발송일</b-th>
            <b-th style="width: 10%">전송</b-th>
          </b-tr>
          <b-td> - </b-td>
          <b-td> - </b-td>
          <b-td><b-form-input v-model="PU_title" /> </b-td>
          <b-td><b-form-input v-model="PU_content" /> </b-td>
          <b-td>
            <input
              type="file"
              accept="image/*"
              @change="uploadFile($event)"
              id="admin-store-file"
            />
          </b-td>
          <b-td>-</b-td>
          <b-td>
            <b-button
              variant="warning"
              style="font-weight: bold"
              @click="sendPush"
            >
              전송
            </b-button>
          </b-td>
        </b-table-simple>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table
          hover
          :items="pushList"
          :per-page="perPage"
          :current-page="currentPage"
          :fields="fields"
          id="admin-notice-table"
        >
          <template #cell(PU_send_cnt)="row">
            {{ row.item.PU_send_cnt | comma }}
          </template>
          <template #cell(PU_path_img)="row">
            <div v-if="row.item.PU_path_img != ''">
              <b-img id="admin-board-img" :src="row.item.PU_path_img2" />
            </div>
          </template>
          <template #cell(삭제)="row">
            <b-button
              size="sm"
              variant="danger"
              @click="deleteBtn(row.item)"
              id="board_edit_btn"
            >
              삭제
            </b-button>
          </template>
          <template #cell(PU_regdate)="row">
            {{ row.item.PU_regdate | yyyyMMddhhmmss }}
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      PU_title: "",
      PU_content: "",
      PU_path_img: "",
      pushList: [],
      fields: [
        { key: "PU_idx", label: "번호", thClass: "w5" },
        { key: "PU_send_cnt", label: "발송수", thClass: "w5" },
        { key: "PU_title", label: "제목", thClass: "w25" },
        { key: "PU_content", label: "메세지", thClass: "w25" },
        { key: "PU_path_img", label: "이미지", thClass: "w15" },
        { key: "PU_regdate", label: "발송일시", thClass: "w15" },
        "삭제",
      ],
      perPage: 8,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getPushList();
  },
  computed: {
    rows() {
      return this.pushList.length;
    },
  },
  methods: {
    uploadFile(e) {
      this.PU_path_img = e.target.files[0];
    },
    async getPushList() {
      try {
        const response = await this.$axios.get(
          `${this.$store.state.host}/admin/user/push/list`
        );
        this.pushList = response.data;
        for (var i = 0; i < this.pushList.length; i++) {
          this.pushList[
            i
          ].PU_path_img2 = `${this.$store.state.host}/${response.data[i].PU_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async sendPush() {
      if (this.PU_title == "") {
        this.$swal("어랏!", "제목을 입력해주세요", "question");
      } else if (this.PU_content == "") {
        this.$swal("어랏!", "메세지를 입력해주세요", "question");
      } else {
        const fd = new FormData();
        fd.append("PU_title", this.PU_title);
        fd.append("PU_content", this.PU_content);
        fd.append("PU_path_img", this.PU_path_img);
        this.$swal({
          title: "Warning",
          text: "모든 회원에게 전송하시겠습니까?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonText: "예",
          cancelButtonText: "아니오",
        }).then(async (result) => {
          if (result.isConfirmed) {
            await this.$axios.post(
              `${this.$store.state.host}/admin/user/push/send`,
              fd
            );
            this.$toast.success("전송 완료되었습니다.");
            this.getPushList();
          }
        });
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(
              `${this.$store.state.host}/admin/user/push/delete/${item.PU_idx}`,
              { PU_path_img: item.PU_path_img }
            );
            this.getPushList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

