<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 업데이트 관리</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" @click="saveAppUpdate"> 수정 </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="height: 200px">앱버전</b-th>
            <b-td><b-form-input v-model="appUpdate.AU_version" style="text-align: center" /></b-td>
          </b-tr>
          <b-tr>
            <b-th style="height: 200px">강제 업데이트 여부</b-th>
            <b-td>
              <b-form-checkbox value="Y" unchecked-value="N" v-model="appUpdate.AU_isUpdate"> </b-form-checkbox>
            </b-td>
          </b-tr>
        </b-table-simple>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      appUpdate: [],
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getUserAppUpdate();
  },

  methods: {
    async getUserAppUpdate() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/appupdate/data`);
        console.log(response.data);
        this.appUpdate = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async saveAppUpdate() {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/appupdate/edit`, {
          AU_version: this.appUpdate.AU_version,
          AU_isUpdate: this.appUpdate.AU_isUpdate,
        });
        if (response.status == 200) {
          this.$swal("Success", "수정 되었습니다.", "success");
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
