<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 견적변동 푸시발송 </span>
      </div>
      <div id="admin-dashboard-borad-top" v-if="selectedType == '출고가'">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 15%">타입</b-th>
            <b-td>
              <b-form-select v-model="selectedType" :options="options1"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>통신사</b-th>
            <b-td>
              <b-form-select v-model="selectedTK" :options="options2"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>가입가능기기</b-th>
            <b-td>
              <div class="row ml-1">
                <div id="change-push-td">제조사</div>
                <div>
                  <b-form-select v-model="selectedCompany" :options="options3"> </b-form-select>
                </div>
                <div id="change-push-td2">단말기타입</div>
                <div>
                  <b-form-select v-if="selectedTK == 1" v-model="selectedProductType" :options="typeList1"> </b-form-select>
                  <b-form-select v-else-if="selectedTK == 2" v-model="selectedProductType" :options="typeList2"> </b-form-select>
                  <b-form-select v-else-if="selectedTK == 3" v-model="selectedProductType" :options="typeList3"> </b-form-select>
                  <b-form-select v-else v-model="selectedProductType" :options="typeList4"> </b-form-select>
                </div>
                <div>
                  <b-button id="change-push-btn2" variant="success" @click="reset()">초기화</b-button>
                </div>
              </div>
              <div class="row ml-1">
                <div id="change-push-div">
                  <b-button variant="info" class="mr-2 mb-2" @click="allSelected()">전체선택</b-button>
                  <b-button variant="info" class="mb-2" @click="allCancelSelected()">전체해제</b-button>
                  <div v-for="(list, i) in phoneFilterList" :key="i">
                    <b-form-checkbox :value="list" v-model="checkedPhoneList">
                      {{ list.PS_name }}
                    </b-form-checkbox>
                  </div>
                </div>
                <div id="change-push-div2">
                  <div v-for="(list, i) in checkedPhoneList" :key="i" id="change-push-div3">
                    {{ list.PS_name }}
                  </div>
                </div>
              </div>
            </b-td>
          </b-tr>
        </b-table-simple>
        <b-button variant="warning" id="change-push-btn" @click="pushMong('chulgo')">푸시발송</b-button>
      </div>
      <div id="admin-dashboard-borad-top" v-if="selectedType == '공시지원금'">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 15%">타입</b-th>
            <b-td>
              <b-form-select v-model="selectedType" :options="options1"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>통신사</b-th>
            <b-td>
              <b-form-select v-model="selectedTK" :options="options2"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>가입가능기기</b-th>
            <b-td>
              <div class="row ml-1">
                <div id="change-push-td">제조사</div>
                <div>
                  <b-form-select v-model="selectedCompany" :options="options3"> </b-form-select>
                </div>
                <div id="change-push-td2">단말기타입</div>
                <div>
                  <b-form-select v-if="selectedTK == 1" v-model="selectedProductType" :options="typeList1"> </b-form-select>
                  <b-form-select v-else-if="selectedTK == 2" v-model="selectedProductType" :options="typeList2"> </b-form-select>
                  <b-form-select v-else-if="selectedTK == 3" v-model="selectedProductType" :options="typeList3"> </b-form-select>
                  <b-form-select v-else v-model="selectedProductType" :options="typeList4"> </b-form-select>
                </div>
                <div>
                  <b-button id="change-push-btn2" variant="success" @click="reset()">초기화</b-button>
                </div>
              </div>
              <div class="row ml-1">
                <div id="change-push-div">
                  <b-button variant="info" class="mr-2 mb-2" @click="allSelected()">전체선택</b-button>
                  <b-button variant="info" class="mb-2" @click="allCancelSelected()">전체해제</b-button>
                  <div v-for="(list, i) in phoneFilterList" :key="i">
                    <b-form-checkbox :value="list" v-model="checkedPhoneList">
                      {{ list.PS_name }}
                    </b-form-checkbox>
                  </div>
                </div>
                <div id="change-push-div2">
                  <div v-for="(list, i) in checkedPhoneList" :key="i" id="change-push-div3">
                    {{ list.PS_name }}
                  </div>
                </div>
              </div>
            </b-td>
          </b-tr>
        </b-table-simple>
        <b-button variant="warning" id="change-push-btn" @click="pushMong('gongsi')">푸시발송</b-button>
      </div>
      <div id="admin-dashboard-borad-top" v-if="selectedType == '프로모션'">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 15%">타입</b-th>
            <b-td>
              <b-form-select v-model="selectedType" :options="options1"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>통신사</b-th>
            <b-td>
              <b-form-select v-model="selectedTK" :options="options2"> </b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>가입가능기기</b-th>
            <b-td>
              <div class="row ml-1">
                <div id="change-push-td">그룹</div>
                <div>
                  <b-form-select v-model="selectedGroup">
                    <b-form-select-option v-for="(list, i) in planGroupFilterList" :key="i" :value="list.PG_idx">
                      {{ list.PG_name }}
                    </b-form-select-option>
                  </b-form-select>
                </div>
                <div>
                  <b-button id="change-push-btn3" variant="success" @click="reset2()">초기화</b-button>
                </div>
              </div>
              <div class="row ml-1">
                <div id="change-push-div">
                  <b-button variant="info" class="mr-2 mb-2" @click="allSelected2()">전체선택</b-button>
                  <b-button variant="info" class="mb-2" @click="allCancelSelected2()">전체해제</b-button>
                  <div v-for="(list, i) in planFilterList" :key="i">
                    <b-form-checkbox :value="list" v-model="checkedPlanList">
                      {{ list.P_plan_name }}
                    </b-form-checkbox>
                  </div>
                </div>
                <div id="change-push-div2">
                  <div v-for="(list, i) in checkedPlanList" :key="i" id="change-push-div3">
                    {{ list.P_plan_name }}
                  </div>
                </div>
              </div>
            </b-td>
          </b-tr>
        </b-table-simple>
        <b-button variant="warning" id="change-push-btn1" @click="pushMong('promo')">푸시발송</b-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedType: "출고가",
      selectedTK: "1",
      selectedCompany: "1",
      selectedProductType: "5G",
      selectedGroup: "1",
      checkedPhoneList: [],
      checkedPlanList: [],
      options1: [
        { value: "출고가", text: "출고가" },
        { value: "공시지원금", text: "공시지원금" },
        { value: "프로모션", text: "프로모션" },
      ],
      options2: [
        { value: "1", text: "SK" },
        { value: "2", text: "KT" },
        { value: "3", text: "LG" },
        { value: "4", text: "7M" },
        { value: "5", text: "MM" },
        { value: "6", text: "UM" },
        { value: "8", text: "HEL_KT" },
        { value: "12", text: "HEL_LG" },
      ],
      options3: [
        { value: "1", text: "삼성" },
        { value: "2", text: "엘지" },
        { value: "3", text: "애플" },
        { value: "4", text: "기타" },
        { value: "5", text: "단말기미구매" },
      ],
      typeList1: [
        { value: "5G", text: "5G" },
        { value: "TABLET_5G", text: "태블릿5G" },
        { value: "4G", text: "4G" },
        { value: "3G", text: "3G" },
        { value: "TABLET", text: "태블릿" },
        { value: "MINI", text: "미니" },
        { value: "GEAR", text: "기어" },
        { value: "KIDS-JOON", text: "키즈준" },
        { value: "T-POCKET-PIE", text: "T포켓파이" },
        { value: "T-PET", text: "T펫" },
        { value: "PREPAYMENT", text: "선불" },
        { value: "USIM", text: "유심" },
      ],
      typeList2: [
        { value: "5G", text: "5G" },
        { value: "TABLET_5G", text: "태블릿5G" },
        { value: "4G", text: "4G" },
        { value: "3G", text: "3G" },
        { value: "TABLET", text: "태블릿" },
        { value: "GEAR", text: "기어" },
        { value: "ROUTER", text: "라우터" },
        { value: "OLLEH-DDOKDDOK", text: "키즈전용" },
        { value: "EGG", text: "에그" },
        { value: "ONLY_DANMAL", text: "GIGA Live TV" },
        { value: "PREPAYMENT", text: "선불" },
        { value: "USIM", text: "유심" },
      ],
      typeList3: [
        { value: "5G", text: "5G" },
        { value: "TABLET_5G", text: "태블릿5G" },
        { value: "4G", text: "4G" },
        { value: "3G", text: "3G" },
        { value: "TABLET", text: "태블릿" },
        { value: "GEAR", text: "기어" },
        { value: "ROUTER", text: "라우터" },
        { value: "KIDS-PHONE", text: "LG 키즈폰" },
        { value: "PREPAYMENT", text: "선불" },
        { value: "USIM", text: "유심" },
      ],
      typeList4: [
        { value: "5G", text: "5G" },
        { value: "TABLET_5G", text: "태블릿5G" },
        { value: "4G", text: "4G" },
        { value: "3G", text: "3G" },
        { value: "TABLET", text: "태블릿" },
        { value: "GEAR", text: "기어" },
        { value: "ROUTER", text: "라우터" },
        { value: "PREPAYMENT", text: "선불" },
        { value: "USIM", text: "유심" },
      ],
      phoneList: [],
      planList: [],
      planGroupList: [],
      pushList: [],
      pushNameList: [],
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getStorePhoneList(), this.getStorePlanList(), this.getStorePlanGroupList();
  },
  methods: {
    async getStorePhoneList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/phone/list`);
        this.phoneList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getStorePlanList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/plan/list`);
        this.planList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getStorePlanGroupList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/plan/group`);
        this.planGroupList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async pushMong(item) {
      if (item != "promo") {
        for (var i = 0; i < this.checkedPhoneList.length; i++) {
          this.pushList[i] = this.checkedPhoneList[i].ps_idx;
        }
        if (this.checkedPhoneList == "") {
          this.$swal("실패!", "한개이상 선택해주세요!.", "error");
          return;
        }
        this.$swal({
          title: "경고",
          text: "PUSH 보내시겠습니까?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonText: "예",
          cancelButtonText: "아니오",
        }).then(async (result) => {
          if (result.isConfirmed) {
            try {
              const response = await this.$axios.post(`${this.$store.state.host}/admin/store/push/${item}`, {
                pushList: this.pushList,
                tk_idx: this.selectedTK,
              });
              if (response.data.result == "Y") {
                this.$swal("성공", response.data.cnt + "명 푸시를 보냈습니다", "success");
                this.checkedPhoneList = [];
                this.pushList = [];
              } else {
                this.checkedPhoneList = [];
                this.pushList = [];
                this.$toast.error("변동에 영향이 있는 견적이 없습니다.");
              }
            } catch (err) {
              console.log(err);
              this.checkedPhoneList = [];
              this.pushList = [];
            }
          }
        });
      } else {
        for (var j = 0; j < this.checkedPlanList.length; j++) {
          this.pushList[j] = this.checkedPlanList[j].P_idx;
        }
        if (this.checkedPlanList == "") {
          this.$swal("실패!", "한개이상 선택해주세요!.", "error");
          return;
        }
        this.$swal({
          title: "경고",
          text: "PUSH 보내시겠습니까?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonText: "예",
          cancelButtonText: "아니오",
        }).then(async (result) => {
          if (result.isConfirmed) {
            try {
              const response = await this.$axios.post(`${this.$store.state.host}/admin/store/push/${item}`, {
                pushList: this.pushList,
                tk_idx: this.selectedTK,
              });
              if (response.data.result == "Y") {
                this.$swal("성공", response.data.cnt + "명 푸시를 보냈습니다", "success");
                this.checkedPlanList = [];
                this.pushList = [];
              } else {
                this.checkedPlanList = [];
                this.pushList = [];
                this.$toast.error("변동에 영향이 있는 견적이 없습니다.");
              }
            } catch (err) {
              console.log(err);
              this.checkedPlanList = [];
              this.pushList = [];
            }
          }
        });
      }
    },
    reset() {
      this.checkedPhoneList = [];
    },
    reset2() {
      this.checkedPlanList = [];
    },
    allSelected() {
      for (var i = 0; i < this.phoneFilterList.length; i++) {
        var check = true;
        for (var n = 0; n < this.checkedPhoneList.length; n++) {
          if (this.checkedPhoneList[n] == this.phoneFilterList[i]) {
            check = false;
            break;
          }
        }
        if (check) {
          this.checkedPhoneList.push(this.phoneFilterList[i]);
        }
      }
    },
    allCancelSelected() {
      for (var i = this.checkedPhoneList.length - 1; i >= 0; i--) {
        var check = false;
        for (var n = 0; n < this.phoneFilterList.length; n++) {
          if (this.checkedPhoneList[i] == this.phoneFilterList[n]) {
            check = true;
            break;
          }
        }
        if (check) {
          this.checkedPhoneList.splice(i, 1);
        }
      }
    },
    allSelected2() {
      for (var i = 0; i < this.planFilterList.length; i++) {
        var check = true;
        for (var n = 0; n < this.checkedPlanList.length; n++) {
          if (this.checkedPlanList[n] == this.planFilterList[i]) {
            check = false;
            break;
          }
        }
        if (check) {
          this.checkedPlanList.push(this.planFilterList[i]);
        }
      }
    },
    allCancelSelected2() {
      for (var i = this.checkedPlanList.length - 1; i >= 0; i--) {
        var check = false;
        for (var n = 0; n < this.planFilterList.length; n++) {
          if (this.checkedPlanList[i] == this.planFilterList[n]) {
            check = true;
            break;
          }
        }
        if (check) {
          this.checkedPlanList.splice(i, 1);
        }
      }
    },
  },
  computed: {
    phoneFilterList: function () {
      return this.phoneList.filter((item) => {
        return item.tk_idx == this.selectedTK && item.mk_idx == this.selectedCompany && item.MD_type == this.selectedProductType;
      });
    },
    planFilterList: function () {
      return this.planList.filter((item) => {
        return item.tk_idx == this.selectedTK && item.pg_idx == this.selectedGroup;
      });
    },
    planGroupFilterList: function () {
      return this.planGroupList.filter((item) => {
        return item.tk_idx == this.selectedTK;
      });
    },
  },
  watch: {
    selectedTK() {
      this.checkedPhoneList = [];
      this.checkedPlanList = [];
    },
    selectedType() {
      this.checkedPhoneList = [];
      this.checkedPlanList = [];
    },
  },
};
</script>

<style></style>
