//통신사 이름가져오기
export function tk_idx(value) {
  var tk_name = "";
  if (value == "") tk_name = "없음";
  if (value == 1) tk_name = "SK";
  if (value == 2) tk_name = "KT";
  if (value == 3) tk_name = "LG";
  if (value == 4) tk_name = "7M";
  if (value == 5) tk_name = "MM";
  if (value == 6) tk_name = "UM";
  if (value == 8) tk_name = "HEL_KT";
  if (value == 12) tk_name = "HEL_LG";
  return tk_name;
}

//천단위콤마
export function comma(value) {
  return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

//날짜 표시
export function yyyyMMdd(value) {
  if (value == "") return "";
  var js_date = new Date(value);
  var year = js_date.getFullYear();
  var month = js_date.getMonth() + 1;
  var day = js_date.getDate();

  if (month < 10) {
    month = "0" + month;
  }
  if (day < 10) {
    day = "0" + day;
  }
  return year + "-" + month + "-" + day;
}

//날짜시간 표시
export function yyyyMMddhhmmss(value) {
  if (value == "") return "";
  var js_date = new Date(value);
  var year = js_date.getFullYear();
  var month = js_date.getMonth() + 1;
  var day = js_date.getDate();
  var hours = js_date.getHours();
  var minutes = js_date.getMinutes();
  var seconds = js_date.getSeconds();

  if (month < 10) {
    month = "0" + month;
  }
  if (day < 10) {
    day = "0" + day;
  }
  return (
    year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds
  );
}

//시간 표시
export function hhmm(value) {
  if (value == "") {
    return "";
  }
  var js_date = new Date(value);
  var hours = js_date.getHours();
  var minutes = js_date.getMinutes();
  if (hours < 10) {
    hours = "0" + hours;
  }
  if (minutes < 10) {
    minutes = "0" + minutes;
  }
  return hours + ":" + minutes;
}

//폰번호 포멧
export function phoneFomatter(value) {
  var formatNum = "";
  if (value.length == 11) {
    formatNum = value.replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3");
  } else if (value.length == 8) {
    formatNum = value.replace(/(\d{4})(\d{4})/, "$1-$2");
  } else {
    if (value.indexOf("02") == 0) {
      formatNum = value.replace(/(\d{2})(\d{4})(\d{4})/, "$1-$2-$3");
    } else {
      formatNum = value.replace(/(\d{3})(\d{3})(\d{4})/, "$1-$2-$3");
    }
  }
  return formatNum;
}
