import Vue from "vue";
import VueRouter from "vue-router";
import router from "./router/router";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import { BootstrapVue, IconsPlugin, BootstrapVueIcons } from "bootstrap-vue";
import App from "./App.vue";
import { store } from "./store/store.js";
import VueCookies from "vue-cookies";
import Print from "vue-print-nb";
import VueApexCharts from "vue-apexcharts";
import Vue2Editor from "vue2-editor";
import axios from "axios";

import VueSweetalert2 from "vue-sweetalert2";
import "sweetalert2/dist/sweetalert2.min.css";
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import { tk_idx, comma, yyyyMMdd, yyyyMMddhhmmss, hhmm, phoneFomatter } from "@/utils/filters.js";
import "./registerServiceWorker";

const options = {
  confirmButtonColor: "#41b882",
  cancelButtonColor: "#ff7674",
};

Vue.use(Vue2Editor);
Vue.use(VueCookies);
Vue.use(VueRouter);
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
Vue.use(BootstrapVueIcons);

Vue.use(store);
Vue.use(Print);
Vue.use(VueApexCharts);
Vue.use(VueSweetalert2, options);
Vue.use(Toast, { hideProgressBar: false, bodyClassName: ["custom-class-1"] });
Vue.prototype.$axios = axios;
Vue.$cookies.config("60*60*1");
Vue.config.productionTip = false;
Vue.component("apexChart", VueApexCharts);

Vue.filter("tk_idx", tk_idx);
Vue.filter("comma", comma);
Vue.filter("yyyyMMdd", yyyyMMdd);
Vue.filter("yyyyMMddhhmmss", yyyyMMddhhmmss);
Vue.filter("hhmm", hhmm);
Vue.filter("phoneFomatter", phoneFomatter);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
