<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 전체 딜 리스트</span>
      </div>
      <div class="row ml-2 mt-1">
        <div class="row" id="admin-store-member-groupInput4">
          <div class="mr-1">
            <b-form-select v-model="selectedUsed" :options="options1"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getSearchDealList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchDealList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="dealList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(DI_count)="row">
              <div v-if="row.item.DI_count >= 1" id="admin-deal-cnt-btn" @click="goEstimateList(row.item.DI_idx)">
                {{ row.item.DI_count }}명
              </div>
              <div v-else id="admin-deal-cnt-btn" @click="goEstimateList(row.item.DI_idx)">0명</div>
            </template>
            <template #cell(DI_before_tk_idx)="row">
              {{ row.item.DI_before_tk_idx | tk_idx }}
            </template>

            <template #cell(DI_after_tk_idx)="row">
              {{ row.item.DI_after_tk_idx | tk_idx }}
            </template>

            <template #cell(상세)="row">
              <b-button size="sm" v-b-modal.modal-deal variant="success" @click="detailBtn(row.item)" id="board_edit_btn"> 상세 </b-button>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
      <b-modal id="modal-deal" title="딜 상세보기" ok-title="닫기" ok-variant="danger" ok-only>
        <b-table-simple id="admin-modal-deal">
          <b-tr>
            <b-th>번호</b-th>
            <b-td>{{ dealRowList.DI_idx }}</b-td>
            <b-th>상태</b-th>
            <b-td>{{ dealRowList.DI_status }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>가입유형</b-th>
            <b-td>{{ dealRowList.DI_join_type }}</b-td>
            <b-th>지원유형</b-th>
            <b-td>{{ dealRowList.DI_support_type }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>현재통신사</b-th>
            <b-td>
              <div v-if="dealRowList.DI_before_tk_idx != ''">
                {{ dealRowList.DI_before_tk_idx | tk_idx }}
              </div>
              <div v-else>-</div>
            </b-td>
            <b-th>개통통신사</b-th>
            <b-td>
              <div v-if="dealRowList.DI_after_tk_idx != ''">
                {{ dealRowList.DI_after_tk_idx | tk_idx }}
              </div>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>희망단말기</b-th>
            <b-td>{{ dealRowList.DI_hope_phone }}</b-td>
            <b-th>희망요금제</b-th>
            <b-td>{{ dealRowList.DI_hope_plan }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>연령타입</b-th>
            <b-td>{{ dealRowList.DI_age_type }}</b-td>
            <b-th>최대할부개월</b-th>
            <b-td>
              <div v-if="dealRowList.DI_max_installment_month != ''">
                {{ dealRowList.DI_max_installment_month }}
              </div>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>결합</b-th>
            <b-td>
              <div v-if="dealRowList.DI_combination != ''">
                {{ dealRowList.DI_combination }}
              </div>
              <div v-else>-</div>
            </b-td>
            <b-th>복지</b-th>
            <b-td>
              <div v-if="dealRowList.DI_welfare != ''">
                {{ dealRowList.DI_welfare }}
              </div>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>기타요청사항</b-th>
            <b-td colspan="3">{{ dealRowList.DI_requests }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>등록인</b-th>
            <b-td>{{ dealRowList.M_name }}</b-td>
            <b-th>등록일자</b-th>
            <b-td>{{ dealRowList.DI_regdate | yyyyMMddhhmmss }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>딜 참여 인원</b-th>
            <b-td colspan="3">
              <div v-if="dealRowList.DI_count >= 1">{{ dealRowList.DI_count }}명</div>
              <div v-else>0명</div>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      dealList: [],
      dealRowList: [],
      fields: [
        { key: "DI_idx", label: "번호", thClass: "w5" },
        { key: "DI_status", label: "상태", thClass: "w10" },
        { key: "DI_join_type", label: "유형", thClass: "w10" },
        { key: "DI_before_tk_idx", label: "현재통신사", thClass: "w10" },
        { key: "DI_after_tk_idx", label: "개통통신사", thClass: "w10" },
        { key: "DI_hope_phone", label: "희망단말기", thClass: "w20" },
        { key: "DI_age_type", label: "연령타입", thClass: "w10" },
        { key: "M_name", label: "등록한사람", thClass: "w10" },
        { key: "DI_count", label: "참여인원", thClass: "w10" },
        "상세",
      ],
      options1: [
        { value: "전체", text: "전체" },
        { value: "진행중", text: "진행중" },
        { value: "종료", text: "종료" },
      ],
      selectedUsed: "전체",
      keyword: "",
      perPage: 13,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getDealList();
  },
  computed: {
    rows() {
      return this.dealList.length;
    },
  },
  methods: {
    async getDealList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/share/deal/list`);
        this.dealList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    async getSearchDealList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/share/deal/search`, {
          selectedUsed: this.selectedUsed,
          keyword: this.keyword,
        });

        this.dealList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    detailBtn(item) {
      this.dealRowList = item;
    },
    goEstimateList(idx) {
      this.$router.push(`/admin/share/estimate/${idx}`).catch(() => {});
    },
  },
};
</script>

<style></style>
