<template>
  <div id="container">
    <div id="member-out-box">
      <div id="member-out-subtitle">
        <div>
          <img src="@/assets/main_img/bananadeal_logo.png" class="rounded" alt="MAINLOGO" id="member-out-mainImg" />
          <span id="member-out-mainfont">바나나딜 회원 탈퇴 신청 페이지</span>
        </div>
      </div>
      <div id="member-out-mainfont2">
        바나나딜을 떠나신다니 아쉽습니다.<br />
        마지막으로 아래 내용을 꼭! 확인해주세요. <br />
        회원 탈퇴는 신청 후 1~3 영업일 내로 처리됩니다. <br />
        회원 탈퇴 후 동일한 아이디를 사용하실 수 없습니다. <br />
        아래의 내용을 작성하시면 회원 탈퇴를 도와드리겠습니다. <br />
      </div>
      <div id="member-layout2">탈퇴할 계정<span id="member-out-font3">*SNS로그인일 경우 SNS 계정을 입력</span></div>
      <div id="member-layout"><b-input v-model="MO_email" placeholder="bananadeal@example.com"></b-input></div>
      <div id="member-layout2">탈퇴사유</div>
      <div id="member-layout">
        <b-form-textarea id="textarea" rows="4" max-rows="5" v-model="MO_reason_content"></b-form-textarea>
      </div>
      <b-button variant="success" @click="outMember" id="member-out-btn">전송</b-button>
      <div id="operations-center">
        <div>고객센터</div>
        <div style="font-size: 20px; font-weight: bold">
          <a href="tel:+07088286745">070-8828-6745</a>
        </div>
        <div>운영시간 : 오전 9:00~오후 6:00</div>
        <div>점심시간 : 오후 12:00~오후 1:00</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      MO_email: "",
      MO_reason_content: "",
    };
  },
  methods: {
    async outMember() {
      try {
        if (this.MO_email == "") {
          this.$swal("어랏!", "이메일을 입력해주세요", "question");
        } else if (this.MO_reason_content == "") {
          this.$swal("어랏!", "탈퇴사유를 입력해주세요", "question");
        } else {
          const response = await this.$axios.post(`${this.$store.state.host}/app/guest/member/out`, {
            MO_email: this.MO_email,
            MO_reason: "6",
            MO_reason_content: this.MO_reason_content,
          });

          if (response.data.msg == "아이디 틀림") {
            this.$swal("Error", "계정를 다시 확인 해주세요.", "error");
          } else {
            this.$swal("성공", "회원 탈퇴가 정상적으로 진행되었습니다.", "success");
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style>
#container {
  background-color: rgb(235, 235, 235);
  display: flex;
  justify-content: center; /* 수평 가운데 정렬 */
  height: 100vh; /* 뷰포트 높이에 맞게 설정 (페이지 높이) */
}
#member-out-box {
  background-color: white;
  margin: 60px 10px 20px 10px;
  border: 2px solid rgb(190, 190, 190);
  width: 500px;
  height: 780px;
  border-radius: 20px;
  padding: 40px 40px;
}
#member-out-mainImg {
  width: 15%;
}
#member-out-subtitle {
  margin-bottom: 20px;
  font-family: "GangwonEduPowerExtraBoldA";
}
#member-out-mainfont {
  margin-left: 20px;
  font-size: 24px;
}
#member-out-mainfont2 {
  text-align: center;
  font-size: 14px;
  margin-bottom: 20px;
}
#operations-center {
  margin-top: 90px;
  text-align: center;
  color: rgb(165, 164, 164);
}
#member-layout {
  margin-bottom: 20px;
}
#member-layout2 {
  font-weight: bold;
}
#member-out-btn {
  width: 100%;
  background-color: #ffd600;
  color: black;
  font-weight: bold;
  border: 1px solid #ffd600;
}
#member-out-font3 {
  color: red;
  font-size: 12px;
  margin-left: 10px;
}
</style>
