<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 이용정지 내역(판매사)</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn3" @click="goStoreMemberList"> 회원리스트 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput2">
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getStoreSearchMemberList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getStoreSearchMemberList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />
        <div v-if="isLoding == true">
          <b-table hover :items="storeMemberList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(SM_pause_duration)="data">
              <span v-if="data.value == '무제한'">무제한</span>
              <span v-else>{{ data.value | yyyyMMdd }} 까지</span>
            </template>
            <template #cell(SM_pause_memo)="row">
              <b-form-textarea id="textarea" v-model="row.item.SM_pause_memo" rows="2" max-rows="6"></b-form-textarea>
            </template>
            <template #cell(수정)="row">
              <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
            </template>
            <template #cell(해제)="row">
              <div v-if="row.item.SM_pause_duration == '무제한'">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="pauseBtn(row.item)" id="board_edit_btn"> 정지해제 </b-button>
              </div>
              <div v-if="today > row.item.SM_pause_duration2">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="pauseBtn(row.item)" id="board_edit_btn"> 정지해제 </b-button>
              </div>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      storeMemberList: [],
      fields: [
        { key: "SM_m_id", label: "몽키컨설팅계정", thClass: "w20" },
        { key: "SM_pause_reason", label: "정지 사유", thClass: "w20" },
        {
          key: "SM_pause_duration",
          label: "정지 기간",
          thClass: "w10",
        },
        { key: "SM_pause_memo", label: "메모", thClass: "w30" },
        "수정",
        "해제",
      ],
      keyword: "",
      perPage: 9,
      filter: null,
      currentPage: 1,
      today: null,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getStoreMemberList();
    this.today = new Date();
  },
  computed: {
    rows() {
      return this.storeMemberList.length;
    },
  },
  methods: {
    async getStoreMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/member/pauselist`);
        this.storeMemberList = response.data;

        for (var i = 0; i < this.storeMemberList.length; i++) {
          this.storeMemberList[i].SM_pause_duration2 = new Date(response.data[i].SM_pause_duration);
        }
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    goStoreMemberList() {
      this.$router.push("/admin/store/member").catch(() => {});
    },

    async pauseBtn(item) {
      let status = { SM_isShow: "USE" };
      if (item.SM_isShow == "STOP_DISABLE") {
        status = { SM_isShow: "DISABLE" };
      }
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/stoppause/${item.SM_m_id}`, status);
        if (response.status == 200) {
          this.getStoreMemberList();
          this.$toast.success(item.SM_m_id + "님" + "정상적으로 정지해제 되었습니다.");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async editBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/editpause/${item.SM_idx}`, {
          SM_pause_memo: item.SM_pause_memo,
        });
        if (response.status == 200) {
          this.getStoreMemberList();
          this.$swal("Success", "정상적으로 수정 되었습니다.", "success");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getStoreSearchMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/member/searchpause`, { keyword: this.keyword });
        this.storeMemberList = response.data;

        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
