<template>
  <div id="admin-dashboard-borad-top3">
    <div id="admin-board-title">1:1 문의</div>
    <span id="admin-dashboard-borad-deal1">
      <table id="admin-dashboard-table3">
        <tr v-for="(list, i) in inqList" :key="i">
          <td>
            <div id="admin-dashboard-table3-td1">
              <li>{{ list.MI_title }}</li>
            </div>
          </td>
          <td id="admin-dashboard-table3-td2">
            {{ list.MI_q_regdate | yyyyMMdd }}
          </td>
        </tr>
      </table>
    </span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inqList: [],
    };
  },
  created() {
    this.getBoardInqList();
  },
  methods: {
    async getBoardInqList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/home/boardinq`);
        this.inqList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

