<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title">앱 소개 이미지 관리 </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 33%">순서</b-th>
            <b-th style="width: 33%">이미지파일<span id="img-set-font">*세로형이미지</span></b-th>
            <b-th style="width: 33%">저장</b-th>
          </b-tr>
          <b-td>
            <b-form-input
              v-model="AII_order"
              style="text-align: center"
              onKeyup="this.value=this.value.replace(/[^0-9]/g,'');"
              placeholder="숫자만 가능"
            />
          </b-td>
          <b-td>
            <input ref="imageUploader" type="file" accept="image/*" @change="uploadFile($event)" id="admin-store-file" />
          </b-td>
          <b-td>
            <b-button variant="warning" style="font-weight: bold" @click="saveImg"> 추가 </b-button>
          </b-td>
        </b-table-simple>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table hover :items="AppInfoList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
          <template #cell(AII_order)="row">
            <b-form-input
              v-model="row.item.AII_order"
              style="text-align: center"
              onKeyup="this.value=this.value.replace(/[^0-9]/g,'');"
              placeholder="숫자만 가능"
            />
          </template>
          <template #cell(이미지파일)>
            <input type="file" accept="image/*" @change="uploadEditFile($event)" id="admin-store-file" />
          </template>
          <template #cell(AII_path_img2)="row">
            <!-- <b-img id="admin-board-img" :src="row.item.AII_path_img2" /> -->
            <a
              style="font-weight: bold"
              :href="row.item.AII_path_img2"
              onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
            >
              미리보기
            </a>
          </template>

          <template #cell(수정)="row">
            <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
          </template>
          <template #cell(삭제)="row">
            <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      AII_order: "",
      AII_path_img: "",
      AII_path_img_edit: "",
      AppInfoList: [],
      fields: [
        { key: "AII_order", label: "순서", thClass: "w25" },
        "이미지파일",
        { key: "AII_path_img2", label: "미리보기", thClass: "w25" },
        "수정",
        "삭제",
      ],
      perPage: 9,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Success", "수정 되었습니다.", "success");
        this.$router.push("/");
      }
    });
    this.getAppInfoList();
  },
  computed: {
    rows() {
      return this.AppInfoList.length;
    },
  },
  methods: {
    uploadFile(e) {
      this.AII_path_img = e.target.files[0];
    },
    uploadEditFile(e) {
      this.AII_path_img_edit = e.target.files[0];
    },
    async getAppInfoList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/appinfoimg/list`);
        this.AppInfoList = response.data;

        for (var i = 0; i < this.AppInfoList.length; i++) {
          this.AppInfoList[i].AII_path_img2 = `${this.$store.state.host}/${response.data[i].AII_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async saveImg() {
      if (this.AII_path_img == "") {
        this.$swal("어랏!", "이미지를 선택해주세요!", "question");
        return;
      }
      const fd = new FormData();
      fd.append("AII_order", this.AII_order);
      fd.append("AII_path_img", this.AII_path_img);

      await this.$axios.post(`${this.$store.state.host}/admin/user/appinfoimg/save`, fd);
      this.$swal("Success", "저장 되었습니다.", "success");
      this.AII_order = "";
      this.AII_path_img = "";
      this.$refs.imageUploader.value = "";
      this.getAppInfoList();
    },

    async editBtn(item) {
      try {
        const fd = new FormData();
        fd.append("AII_order", item.AII_order);
        fd.append("AII_path_img", item.AII_path_img);
        fd.append("AII_path_img_edit", this.AII_path_img_edit);

        await this.$axios.post(`${this.$store.state.host}/admin/user/appinfoimg/edit/${item.AII_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.AII_path_img_edit = "";
        this.getAppInfoList();
      } catch (err) {
        console.log(err);
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/appinfoimg/delete/${item.AII_idx}`, {
              AII_path_img: item.AII_path_img,
            });
            this.getAppInfoList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
