<template>
  <div>
    <div id="admin-side-menu-box">
      <div id="side-main-img" @click="goHome">
        <img src="@/assets/main_img/banana2.png" class="rounded" alt="MAINLOGO" id="admin-side-mainImg" />
        <span id="side_title_font">Banana Deal</span>
        <p style="margin-left: 50px; font-size: 20px; color: rgba(80, 41, 255, 0.603)">테스트서버</p>
      </div>
      <div id="admin-side-menu-title" @click="goDashBoard" :style="color1">
        <i class="bi bi-bar-chart-line-fill" id="admin-side-icon1"></i>대시보드
      </div>

      <div id="admin-side-menu-title2" @click="openSeller" :style="color2">
        <i class="bi bi-person-lines-fill" id="admin-side-icon2"></i>판매사용

        <div id="admin-side-menu-subtitle-top" v-if="seller == true">
          <div id="admin-side-menu-subtitle" @click="goStorePage('notice')" :style="color21">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>공지사항
          </div>
          <div id="admin-side-menu-subtitle2" @click="goStorePage('member')" :style="color22">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>회원관리
          </div>
          <div id="admin-side-menu-subtitle3" @click="goStorePage('report')" :style="color23">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>신고관리
          </div>
          <div id="admin-side-menu-subtitle4" @click="goStorePage('sale')" :style="color24">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>지원금 상한액관리
          </div>
          <div id="admin-side-menu-subtitle44" @click="goStorePage('changepush')" :style="color25">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>견적변동 푸시발송
          </div>
        </div>
      </div>

      <div id="admin-side-menu-title3" @click="openBuyer" :style="color3">
        <i class="bi bi-person-workspace" id="admin-side-icon3"></i>고객용
        <div id="admin-side-menu-subtitle-top" v-if="buyer == true">
          <div id="admin-side-menu-subtitle5" @click="goUserPage('appupdate')" :style="color31">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>업데이트 관리
          </div>
          <div id="admin-side-menu-subtitle6" @click="goUserPage('appinfoimg')" :style="color32">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>앱소개 이미지 관리
          </div>
          <div id="admin-side-menu-subtitle7" @click="goUserPage('mainpopup')" :style="color33">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>메인 팝업 관리
          </div>
          <div id="admin-side-menu-subtitle77" @click="goUserPage('mainbanner')" :style="color333">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>메인 배너 관리
          </div>
          <div id="admin-side-menu-subtitle8" @click="goUserPage('push')" :style="color34">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>푸시 관리
          </div>
          <div id="admin-side-menu-subtitle9" @click="goUserPage('report2')" :style="color35">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>신고 관리
          </div>
          <div id="admin-side-menu-subtitle10" @click="goUserPage('member2')" :style="color36">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>회원 관리
          </div>
          <div id="admin-side-menu-subtitle11" @click="goUserPage('memberout')" :style="color37">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>회원 탈퇴 관리
          </div>
          <div id="admin-side-menu-subtitle12" @click="goUserPage('notice2')" :style="color38">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>공지사항
          </div>
          <div id="admin-side-menu-subtitle13" @click="goUserPage('event')" :style="color39">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>이벤트
          </div>
          <div id="admin-side-menu-subtitle14" @click="goUserPage('inq')" :style="color310">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>1:1 문의
          </div>
          <div id="admin-side-menu-subtitle15" @click="goUserPage('favorite')" :style="color311">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>자주하는 질문
          </div>
          <div id="admin-side-menu-subtitle16" @click="goUserPage('terms')" :style="color312">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>이용약관
          </div>
        </div>
      </div>
      <div id="admin-side-menu-title4" @click="openShare" :style="color4">
        <i class="bi bi-share-fill" id="admin-side-icon4"></i>공통
        <div id="admin-side-menu-subtitle-top" v-if="share == true">
          <div id="admin-side-menu-subtitle17" @click="goSharePage('deal')" :style="color41">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>전체 딜 리스트
          </div>
          <div id="admin-side-menu-subtitle18" @click="goSharePage('allreview')" :style="color42">
            <i class="bi bi-pin-fill" id="admin-side-icon6"></i>전체 후기 보기
          </div>
        </div>
      </div>
      <div id="admin-side-menu-title" @click="goAdimAdd" :style="color5">
        <i class="bi bi-person-square" id="admin-color"></i>
        관리자 등록
      </div>
      <div id="admin-side-menu-title5" :style="color6" @click="goMonkeyPage">
        <i class="bi bi-bookmark-check-fill" id="admin-side-icon5"></i>
        몽컨 관리자 바로가기
      </div>
    </div>
    <div id="admin-side-login-name" @click="isLogin = !isLogin">
      <i class="bi bi-person-square" id="admin-color"></i>
      {{ $store.state.userStore.AM_name }}
      {{ $store.state.userStore.AM_rank }}
    </div>
    <div v-if="isLogin == true" @click="logOut" id="logout-btn">로그아웃</div>
  </div>
</template>

<script>
import { deleteCookie } from "@/utils/cookies";
export default {
  data() {
    return {
      isLogin: false,
      seller: false,
      buyer: false,
      share: false,
      path: "",
      color1: "background-color: white;",
      color2: "background-color: white;",
      color3: "background-color: white;",
      color4: "background-color: white;",
      color5: "background-color: white;",
      color6: "background-color: white;",
      color21: "background : #white",
      color22: "background : #white",
      color23: "background : #white",
      color24: "background : #white",
      color25: "background : #white",
      color31: "background : #white",
      color32: "background : #white",
      color33: "background : #white",
      color333: "background : #white",
      color34: "background : #white",
      color35: "background : #white",
      color36: "background : #white",
      color37: "background : #white",
      color38: "background : #white",
      color39: "background : #white",
      color310: "background : #white",
      color311: "background : #white",
      color312: "background : #white",
      color41: "background : #white",
      color42: "background : #white",
    };
  },
  created() {
    this.path = window.location.pathname.split("/").pop();
    if (this.path == "home") {
      this.color1 = "background : #fff7cc";
    }
    if (this.path == "notice") {
      this.seller = true;
      this.color2 = "background : #fff7cc";
      this.color21 = "background : #ffd600";
    }
    if (this.path == "member") {
      this.seller = true;
      this.color2 = "background : #fff7cc";
      this.color22 = "background : #ffd600";
    }
    if (this.path == "report" || this.path == "review") {
      this.seller = true;
      this.color2 = "background : #fff7cc";
      this.color23 = "background : #ffd600";
    }
    if (this.path == "sale") {
      this.seller = true;
      this.color2 = "background : #fff7cc";
      this.color24 = "background : #ffd600";
    }
    if (this.path == "changepush") {
      this.seller = true;
      this.color2 = "background : #fff7cc";
      this.color25 = "background : #ffd600";
    }
    if (this.path == "appupdate") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color31 = "background : #ffd600";
    }
    if (this.path == "appinfoimg") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color32 = "background : #ffd600";
    }
    if (this.path == "mainpopup") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color33 = "background : #ffd600";
    }
    if (this.path == "mainbanner") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color333 = "background : #ffd600";
    }
    if (this.path == "push") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color34 = "background : #ffd600";
    }

    if (this.path == "report2" || this.path == "review2" || this.path == "store") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color35 = "background : #ffd600";
    }
    if (this.path == "member2" || this.path == "pauselist2") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color36 = "background : #ffd600";
    }
    if (this.path == "memberout") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color37 = "background : #ffd600";
    }
    if (this.path == "notice2") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color38 = "background : #ffd600";
    }
    if (this.path == "event" || this.path == "join") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color39 = "background : #ffd600";
    }
    if (this.path == "inq") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color310 = "background : #ffd600";
    }
    if (this.path == "favorite") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color311 = "background : #ffd600";
    }
    if (this.path == "terms") {
      this.buyer = true;
      this.color3 = "background : #fff7cc";
      this.color312 = "background : #ffd600";
    }
    if (this.path == "deal" || this.path == "estimate") {
      this.share = true;
      this.color4 = "background : #fff7cc";
      this.color41 = "background : #ffd600";
    }
    if (this.path == "allreview") {
      this.share = true;
      this.color4 = "background : #fff7cc";
      this.color42 = "background : #ffd600";
    }
    if (this.path == "list") {
      this.color5 = "background : #fff7cc";
    }
  },
  methods: {
    goHome() {
      this.$router.push("/admin/home").catch(() => {});
    },
    goDashBoard() {
      this.seller = false;
      this.buyer = false;
      this.share = false;
      this.$router.push("/admin/home").catch(() => {});
    },
    goStorePage(link) {
      this.$router.push(`/admin/store/${link}`).catch(() => {});
    },
    goUserPage(link) {
      this.$router.push(`/admin/user/${link}`).catch(() => {});
    },
    goSharePage(link) {
      this.$router.push(`/admin/share/${link}`).catch(() => {});
    },
    goMonkeyPage() {
      window.open("http://imsi.monkeyphone.co.kr/_admin/adminMemberManager", "_blank");
    },
    openSeller() {
      if (this.seller == true) {
        this.seller = !this.seller;
      } else {
        this.seller = !this.seller;
        this.buyer = false;
        this.share = false;
      }
    },
    openBuyer() {
      if (this.buyer == true) {
        this.buyer = !this.buyer;
      } else {
        this.seller = false;
        this.buyer = !this.buyer;
        this.share = false;
        this.color1 = "background-color: white;";
        this.color3 = "background-color: white;";
        this.color4 = "background-color: white;";
        this.color5 = "background-color: white;";
      }
    },
    openShare() {
      if (this.share == true) {
        this.share = !this.share;
      } else {
        this.seller = false;
        this.buyer = false;
        this.share = !this.share;
      }
    },
    goAdimAdd() {
      this.seller = false;
      this.buyer = false;
      this.share = false;
      this.color1 = "background-color: white;";
      this.$router.push("/admin/list").catch(() => {});
    },

    logOut() {
      this.$store.commit("userStore/logout");
      deleteCookie("token");
      this.$router.push(`/`).catch(() => {});
    },
  },
};
</script>

<style></style>
