<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 관리자 등록 </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 10%">번호</b-th>
            <b-th style="width: 20%">아이디</b-th>
            <b-th style="width: 20%">비번</b-th>
            <b-th style="width: 20%">이름</b-th>
            <b-th style="width: 20%">직책</b-th>
            <b-th style="width: 10%">저장</b-th>
          </b-tr>
          <b-td> - </b-td>
          <b-td>
            <b-form-input v-model="AM_id" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_pw" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_name" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_rank" />
          </b-td>
          <b-td>
            <b-button
              variant="warning"
              style="font-weight: bold"
              @click="saveNotice"
            >
              저장
            </b-button>
          </b-td>
        </b-table-simple>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table
          :items="adminMemberList"
          :per-page="perPage"
          :current-page="currentPage"
          :fields="fields"
          id="admin-notice-table"
        >
          <template #cell(AM_idx)="data">
            <b id="mypage-orderNumer-fontcolor">{{ data.value }}</b>
          </template>
          <template #cell(비번)="row">
            <b-form-input v-model="row.item.AM_pw" />
          </template>
          <template #cell(AM_rank)="row">
            <b-form-input v-model="row.item.AM_rank" />
          </template>
          <template #cell(AM_reg_date)="row">
            {{ row.item.AM_reg_date | yyyyMMdd }}
          </template>

          <template #cell(수정)="row">
            <b-button
              size="sm"
              variant="primary"
              @click="editBtn(row.item)"
              id="board_edit_btn"
            >
              수정
            </b-button>
          </template>
          <template #cell(삭제)="row">
            <b-button
              size="sm"
              variant="danger"
              @click="deleteBtn(row.item)"
              id="board_edit_btn"
            >
              삭제
            </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      AM_id: "",
      AM_pw: "",
      AM_name: "",
      AM_rank: "",
      adminMemberList: [],
      fields: [
        { key: "AM_idx", label: "번호", thClass: "w5" },
        { key: "AM_id", label: "아이디", thClass: "w20" },
        "비번",
        { key: "AM_name", label: "이름", thClass: "w15" },
        { key: "AM_rank", label: "직책", thClass: "w15" },
        { key: "AM_reg_date", label: "등록일", thClass: "w15" },
        "수정",
        "삭제",
      ],
      perPage: 9,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getAdminMemberList();
  },
  computed: {
    rows() {
      return this.adminMemberList.length;
    },
  },
  methods: {
    async getAdminMemberList() {
      try {
        const response = await this.$axios.get(
          `${this.$store.state.host}/admin/list/data`
        );
        this.adminMemberList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async saveNotice() {
      if (this.AM_id == "") {
        this.$swal("어랏!", "아이디를 작성해 주세요", "question");
        return;
      }
      if (this.AM_pw == "") {
        this.$swal("어랏!", "비밀번호를 작성해 주세요", "question");
        return;
      }
      if (this.AM_name == "") {
        this.$swal("어랏!", "이름을 작성해 주세요", "question");
        return;
      }
      if (this.AM_rank == "") {
        this.$swal("어랏!", "직책을 작성해 주세요", "question");
        return;
      }
      const response = await this.$axios.post(
        `${this.$store.state.host}/admin/list/create`,
        {
          AM_id: this.AM_id,
          AM_pw: this.AM_pw,
          AM_name: this.AM_name,
          AM_rank: this.AM_rank,
        }
      );
      if (response.data.msg == "같은 아이디가 존재합니다.") {
        this.$swal("Error", "같은 아이디가 존재합니다.", "error");
      } else {
        this.$swal("Success", "저장 되었습니다.", "success");
        this.AM_id = "";
        this.AM_pw = "";
        this.AM_name = "";
        this.AM_rank = "";
        this.getAdminMemberList();
      }
    },
    async editBtn(item) {
      try {
        await this.$axios.post(`${this.$store.state.host}/admin/list/edit`, {
          AM_idx: item.AM_idx,
          AM_pw: item.AM_pw,
          AM_rank: item.AM_rank,
        });
        this.$swal("Success", "수정 되었습니다.", "success");
        this.getAdminMemberList();
      } catch (err) {
        console.log(err);
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(
              `${this.$store.state.host}/admin/list/delete`,
              { AM_idx: item.AM_idx }
            );
            this.getAdminMemberList();
            this.$toast.success("삭제되었습니다");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
