<!-- eslint-disable vue/no-use-v-if-with-v-for,vue/no-confusing-v-for-v-if -->
<template>
  <div id="admin-dashboard-borad-top4">
    <div class="mb-2">
      <span id="admin-board-title">신고현황</span>
      <span id="admin-board-btn">
        <b-button size="sm" :variant="btnColor1" class="mr-1" @click="showStoreRepoter('STORE')">판매자</b-button>
        <b-button size="sm" :variant="btnColor2" @click="showStoreRepoter('USER')">고객</b-button>
      </span>
    </div>
    <div v-if="btnStoreUser == 'USER'">
      <div id="admin-board-border">
        <div id="admin-board-title-sub">매장신고</div>
        <li v-for="(list, i) in UserReportList" :key="i" v-if="list.BRU_type == 'STORE'" id="admin-board-li">
          [{{ list.BRU_sm_m_id }}]
          {{ list.BRU_title }}
        </li>
      </div>
      <div id="admin-board-border">
        <div id="admin-board-title-sub">후기신고</div>
        <li v-for="(list, i) in UserReportList" :key="i" v-if="list.BRU_type == 'REVIEW'" id="admin-board-li">
          [{{ list.BRU_sm_m_id }}]
          {{ list.BRU_title }}
        </li>
      </div>
      <div id="admin-board-border">
        <div id="admin-board-title-sub">채팅신고</div>
        <li v-for="(list, i) in UserReportList" :key="i" v-if="list.BRU_type == 'CHAT'" id="admin-board-li">
          [{{ list.BRU_sm_m_id }}]
          {{ list.BRU_title }}
        </li>
      </div>
    </div>
    <div v-if="btnStoreUser == 'STORE'">
      <div id="admin-board-border">
        <div id="admin-board-title-sub">후기신고</div>
        <li v-for="(list, i) in StoreReportList" :key="i" v-if="list.BRS_type == 'REVIEW'" id="admin-board-li">
          [{{ list.M_name }}]
          {{ list.BRS_title }}
        </li>
      </div>
      <div id="admin-board-border">
        <div id="admin-board-title-sub">채팅신고</div>
        <li v-for="(list, i) in StoreReportList" :key="i" v-if="list.BRS_type == 'CHAT'" id="admin-board-li">
          [{{ list.BRS_sm_m_id }}]
          {{ list.BRS_title }}
        </li>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      UserReportList: [],
      StoreReportList: [],
      btnStoreUser: "USER",
      btnColor1: "outline-warning",
      btnColor2: "warning",
    };
  },
  created() {
    this.getBoardReportUserList();
    this.getBoardReportStoreList();
  },
  methods: {
    async getBoardReportUserList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/home/boardreport/USER`);
        this.UserReportList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async getBoardReportStoreList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/home/boardreport/STORE`);
        this.StoreReportList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    showStoreRepoter(repoter) {
      this.btnStoreUser = repoter;
      if (repoter == "STORE") {
        this.btnColor1 = "warning";
        this.btnColor2 = "outline-warning";
      } else {
        this.btnColor1 = "outline-warning";
        this.btnColor2 = "warning";
      }
    },
  },
};
</script>

<style></style>
