<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 전체 후기 보기 </span>
      </div>
      <div class="row" id="admin-review-top">
        <div class="mr-1">
          <b-form-select v-model="selectedItem" :options="options"></b-form-select>
        </div>
        <div>
          <b-form-input v-model="keyword" @keyup.enter="getSearchReviewList"></b-form-input>
        </div>
        <div>
          <b-button variant="warning" class="ml-1" @click="getSearchReviewList">
            <i class="bi bi-search"></i>
          </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />
        <div v-if="isLoding == true">
          <b-table hover :items="reviewList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(RU_regdate)="row">
              {{ row.item.RU_regdate | yyyyMMddhhmmss }}
            </template>
            <template #cell(RU_content)="row">
              <div id="admin-review-content">{{ row.item.RU_content }}</div>
            </template>
            <template #cell(상세)="row">
              <b-button size="sm" variant="success" v-b-modal.modal-event @click="detailModalBtn(row.item)" id="board_edit_btn">
                상세
              </b-button>
            </template>
            <template #cell(비활성화)="row">
              <b-button
                v-if="row.item.RU_activate == 'Y'"
                size="sm"
                variant="danger"
                @click="editActiveBtn(row.item.RU_idx, 'N')"
                id="board_edit_btn"
              >
                비활성화
              </b-button>
              <b-button
                v-if="row.item.RU_activate == 'N'"
                size="sm"
                variant="primary"
                @click="editActiveBtn(row.item.RU_idx, 'Y')"
                id="board_edit_btn"
              >
                활성화
              </b-button>
            </template>
          </b-table>
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
    <b-modal
      v-if="reviewRowList.RU_activate == 'Y'"
      id="modal-event"
      title="후기 상세 보기"
      cancel-title="닫기"
      ok-variant="danger"
      ok-title="비활성화"
      @ok="editActiveBtn(reviewRowList.RU_idx, 'N')"
    >
      <b-table-simple id="admin-modal-deal">
        <b-table-simple>
          <b-tr>
            <b-th>번호</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_idx }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>작성자닉네임</b-th>
            <b-td colspan="3">{{ reviewRowList.M_name }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>점수</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_point }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>내용</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_content }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>첨부이미지</b-th>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img1 != ''"
                :href="reviewRowList.RU_path_img1_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img2 != ''"
                :href="reviewRowList.RU_path_img2_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img3 != ''"
                :href="reviewRowList.RU_path_img3_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>작성일</b-th>
            <b-td colspan="3">
              {{ reviewRowList.RU_regdate | yyyyMMddhhmmss }}
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>매장명</b-th>
            <b-td colspan="3">{{ reviewRowList.SM_store_name }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>답글</b-th>
            <b-td colspan="3">
              <div v-if="reviewRowList.RA_answer == null">-</div>
              <div v-else>{{ reviewRowList.RA_answer }}</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답글작성일</b-th>
            <b-td colspan="3">
              <div v-if="reviewRowList.RA_regdate == null">-</div>
              <div v-else>{{ reviewRowList.RA_regdate | yyyyMMddhhmmss }}</div>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-table-simple>
    </b-modal>
    <b-modal
      v-if="reviewRowList.RU_activate == 'N'"
      id="modal-event"
      title="후기 상세 보기"
      cancel-title="닫기"
      ok-variant="primary"
      ok-title="활성화"
      @ok="editActiveBtn(reviewRowList.RU_idx, 'Y')"
    >
      <b-table-simple id="admin-modal-deal">
        <b-table-simple>
          <b-tr>
            <b-th>번호</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_idx }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>작성자닉네임</b-th>
            <b-td colspan="3">{{ reviewRowList.M_name }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>점수</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_point }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>내용</b-th>
            <b-td colspan="3">{{ reviewRowList.RU_content }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>첨부이미지</b-th>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img1 != ''"
                :href="reviewRowList.RU_path_img1_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img2 != ''"
                :href="reviewRowList.RU_path_img2_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td>
              <a
                v-if="reviewRowList.RU_path_img3 != ''"
                :href="reviewRowList.RU_path_img3_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>작성일</b-th>
            <b-td colspan="3">
              {{ reviewRowList.RU_regdate | yyyyMMddhhmmss }}
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>매장명</b-th>
            <b-td colspan="3">{{ reviewRowList.SM_store_name }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>답글</b-th>
            <b-td colspan="3">{{ reviewRowList.RA_answer }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>답글작성일</b-th>
            <b-td colspan="3">
              {{ reviewRowList.RA_regdate | yyyyMMddhhmmss }}
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-table-simple>
    </b-modal>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      reviewList: [],
      reviewRowList: [],
      fields: [
        { key: "RU_idx", label: "번호", thClass: "w5" },
        { key: "M_name", label: "작성자 닉네임", thClass: "w10" },
        { key: "SM_store_name", label: "평가 매장명", thClass: "w15" },
        { key: "RU_point", label: "점수", thClass: "w5" },
        { key: "RU_content", label: "내용", thClass: "w30" },
        { key: "RU_regdate", label: "작성일", thClass: "w15" },
        "상세",
        "비활성화",
      ],
      options: [
        { value: "SM_store_name", text: "매장명" },
        { value: "M_name", text: "작성자" },
      ],
      perPage: 13,
      currentPage: 1,
      selectedItem: "SM_store_name",
      keyword: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getReviewList();
  },
  computed: {
    rows() {
      return this.reviewList.length;
    },
  },

  methods: {
    async getReviewList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/share/review/list`);
        this.reviewList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.reviewList.length; i++) {
          this.reviewList[i].RU_path_img1_1 = `${this.$store.state.host}/${response.data[i].RU_path_img1}`;
          this.reviewList[i].RU_path_img2_1 = `${this.$store.state.host}/${response.data[i].RU_path_img2}`;
          this.reviewList[i].RU_path_img3_1 = `${this.$store.state.host}/${response.data[i].RU_path_img3}`;
        }
      } catch (err) {
        console.log(err);
      }
    },

    async getSearchReviewList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/share/review/search`, {
          selectedItem: this.selectedItem,
          keyword: this.keyword,
        });
        this.reviewList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.reviewList.length; i++) {
          this.reviewList[i].RU_path_img1_1 = `${this.$store.state.host}/${response.data[i].RU_path_img1}`;
          this.reviewList[i].RU_path_img2_1 = `${this.$store.state.host}/${response.data[i].RU_path_img2}`;
          this.reviewList[i].RU_path_img3_1 = `${this.$store.state.host}/${response.data[i].RU_path_img3}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    detailModalBtn(item) {
      this.reviewRowList = item;
    },
    async editActiveBtn(idx, value) {
      this.$swal({
        title: "Warning",
        text: "수정하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            const response = await this.$axios.post(`${this.$store.state.host}/admin/share/review/edit`, {
              RU_idx: idx,
              RU_activate: value,
            });
            if (response.status == 200) {
              this.$toast.success("수정되었습니다");
              this.getReviewList();
            } else {
              this.$toast.error("실패했습니다.");
            }
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>
<style></style>
