<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 회원(고객)</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn1" v-b-modal.modal-1> 푸시발송 </b-button>
          <b-button id="admin-store-member-topBtn2" @click="goPauseList"> 이용정지내역 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput10">
          <div class="mr-1">
            <b-form-select v-model="selectedUsed" :options="options1"></b-form-select>
          </div>
          <div class="mr-1">
            <b-form-select v-model="selectedID" :options="options2"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getUserSearchMemberList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getUserSearchMemberList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="userMemberList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(M_path_img)="row">
              <div v-if="row.item.M_path_img != ''">
                <b-img id="admin-member-img" :src="row.item.M_path_img2" />
              </div>
            </template>
            <template #cell(M_regdate)="row">
              {{ row.item.M_regdate | yyyyMMdd }}
            </template>
            <template #cell(M_memo)="row">
              <b-form-textarea id="textarea" v-model="row.item.M_memo" rows="2" max-rows="6"></b-form-textarea>
            </template>
            <template #cell(수정)="row">
              <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
            </template>
            <template #cell(이용정지)="row">
              <div v-if="row.item.M_isUsed == '이용중'">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="usedBtn(row.item)" id="board_edit_btn"> 이용정지 </b-button>
              </div>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>

      <!-- 모달 부분 -->
      <b-modal id="modal-1" title="개별 푸시 발송" cancel-title="닫기" cancel-variant="danger" ok-title="발송" @ok="goPush">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-td class="text-center font-weight-bold" colspan="2"> 메세지를 입력해주세요 </b-td>
          </b-tr>
          <b-tr>
            <b-th>이메일</b-th>
            <b-td>
              <b-input-group>
                <b-form-input size="sm" v-model="modalPushId" />
                <b-input-group-append>
                  <b-button size="sm" variant="warning" @click="pushCheckId">
                    <i class="bi bi-search"></i>
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td><b-form-input size="sm" v-model="modalPushTitle" /></b-td>
          </b-tr>
          <b-tr>
            <b-th>내용</b-th>
            <b-td>
              <b-form-textarea id="textarea" rows="4" max-rows="6" v-model="modalPushContent"> </b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile($event)" id="admin-store-file-modal" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>

      <b-modal id="modal-2" title="이용중지(판매사)" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="goPause">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th>이메일</b-th>
            <b-td>
              <b-form-input size="sm" v-model="modalPauseId" readonly />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>정지기간</b-th>
            <b-td>
              <b-form-select v-model="modalPauseDuration" size="sm" :options="options3"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>정지사유</b-th>
            <b-td>
              <b-form-input size="sm" v-model="modalPauseReason" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>메모</b-th>
            <b-td>
              <b-form-textarea id="textarea" rows="4" max-rows="6" v-model="modalPauseMemo"> </b-form-textarea>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      userMemberList: [],
      fields: [
        {
          key: "M_name",
          label: "닉네임",
          thClass: "w15",
        },
        {
          key: "M_email",
          label: "이메일",
          thClass: "w15",
        },
        {
          key: "M_model",
          label: "사용단말기",
          thClass: "w10",
        },
        { key: "M_path_img", label: "썸네일", thClass: "w10" },
        { key: "M_regdate", label: "가입일", thClass: "w10" },
        { key: "M_isUsed", label: "상태", thClass: "w10" },
        { key: "M_memo", label: "메모", thClass: "w15" },
        "수정",
        "이용정지",
      ],
      options1: [
        { value: "이용중", text: "이용중" },
        { value: "비활성화", text: "비활성화" },
        { value: "이용정지", text: "이용정지" },
      ],
      options2: [
        { value: "닉네임", text: "닉네임" },
        { value: "연락처", text: "연락처" },
      ],
      options3: [
        { value: 1, text: "1일" },
        { value: 3, text: "3일" },
        { value: 7, text: "7일" },
        { value: 30, text: "30일" },
        { value: "무제한", text: "무제한" },
      ],
      selectedUsed: "이용중",
      selectedID: "닉네임",
      keyword: "",
      perPage: 8,
      currentPage: 1,
      idChecked: false,
      modalPushId: "",
      modalPushTitle: "",
      modalPushContent: "",
      modalPushPathImg: "",
      modalPauseId: "",
      modalPauseDuration: "",
      modalPauseReason: "",
      modalPauseMemo: "",
      token: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getUserMemberList();
  },
  computed: {
    rows() {
      return this.userMemberList.length;
    },
  },
  methods: {
    uploadFile(e) {
      this.modalPushPathImg = e.target.files[0];
    },
    async getUserMemberList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/member`);
        this.userMemberList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.userMemberList.length; i++) {
          this.userMemberList[i].M_path_img2 = `${this.$store.state.host}/${response.data[i].M_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getUserSearchMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/search`, {
          selectedUsed: this.selectedUsed,
          selectedID: this.selectedID,
          keyword: this.keyword,
        });
        this.userMemberList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.userMemberList.length; i++) {
          this.userMemberList[i].M_path_img2 = `${this.$store.state.host}/${response.data[i].M_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async editBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/edit/${item.M_idx}`, { M_memo: item.M_memo });
        if (response.status == 200) {
          this.$swal("Success", "수정 되었습니다.", "success");
          this.isLoding = false;
          this.getUserMemberList();
          this.isLoding = true;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async pushCheckId() {
      this.idChecked = false;
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/member/find/${this.modalPushId}`);
        if (response.data != "") {
          this.$swal("Success", "매칭완료: 해당 이매일은 존재합니다.", "success");
          this.token = response.data.M_uuid;
          this.idChecked = true;
        } else {
          this.$swal("Error", "이메일을 다시 확인 해주세요..", "error");
        }
      } catch (err) {
        console.log(err);
      }
    },
    usedBtn(item) {
      this.modalPauseId = item.M_email;
    },
    async goPush(bvModalEvent) {
      if (this.idChecked == false || this.modalPushId == "") {
        this.$swal("어랏!", "이메일 확인 부탁드립니다", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPushTitle == "") {
        this.$swal("어랏!", "제목을 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPushContent == "") {
        this.$swal("어랏!", "내용을 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          const fd = new FormData();
          fd.append("title", this.modalPushTitle);
          fd.append("msg", this.modalPushContent);
          fd.append("img", this.modalPushPathImg);
          fd.append("token", this.token);

          const response = await this.$axios.post(`${this.$store.state.host}/admin/store/push`, fd);

          if (response.status == 200) {
            this.$swal("Success", "푸시를 성공적으로 보냈습니다!", "success");
            this.modalPushId = "";
            this.modalPushTitle = "";
            this.modalPushContent = "";
            this.idChecked = false;
            this.token = "";
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    async goPause(bvModalEvent) {
      if (this.modalPauseDuration == "") {
        this.$swal("어랏!", "정지 기간을 선택해주세요", "question");
        bvModalEvent.preventDefault();
      } else if (this.modalPauseReason == "") {
        this.$swal("어랏!", "정지 사유를 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/pause/${this.modalPauseId}`, {
            M_pause_duration: this.modalPauseDuration,
            M_pause_reason: this.modalPauseReason,
            M_pause_memo: this.modalPauseMemo,
            M_isUsed: "이용정지",
          });
          if (response.status == 200) {
            this.$swal("Success", "저장 되었습니다.", "success");
            this.isLoding = false;
            this.getUserMemberList();
            this.isLoding = true;
            this.modalPauseDuration = "";
            this.modalPauseReason = "";
            this.modalPauseMemo = "";
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    goPauseList() {
      this.$router.push("/admin/user/member2/pauselist2").catch(() => {});
    },
  },
};
</script>

<style></style>
