<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 이벤트 </span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" v-b-modal.modal-event @click="addEvent"> 추가 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput8">
          <div class="mr-1">
            <b-form-select v-model="selectedItem" :options="options"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getSearchEventList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchEventList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="eventList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(BE_period_start)="row">
              {{ row.item.BE_period_start | yyyyMMdd }} ~
              {{ row.item.BE_period_end | yyyyMMdd }}
            </template>
            <template #cell(BE_path_img1)="row">
              <b-img v-if="row.item.BE_path_img1 != ''" id="admin-user-popup-img" :src="row.item.BE_path_img1_1" />
            </template>
            <template #cell(상세)="row">
              <b-button size="sm" variant="success" v-b-modal.modal-event-edit @click="editModalBtn(row.item)" id="board_edit_btn">
                상세
              </b-button>
            </template>
            <template #cell(참가자)="row">
              <b-button
                v-if="row.item.BE_type == '참여형'"
                size="sm"
                variant="primary"
                @click="eventJoinList(row.item)"
                id="board_edit_btn"
              >
                참가자 확인
              </b-button>
            </template>
            <template #cell(삭제)="row">
              <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
            </template>
          </b-table>
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>

    <!-- 이벤트 추가 모달 -->
    <b-modal id="modal-event" title="이벤트 추가" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="saveEvent">
      <!-- <b-button
        size="sm"
        class="mb-2"
        variant="warning"
        @click="modalAddImgBtn"
      >
        이미지 추가
      </b-button> -->
      <b-table-simple id="user-modal-event">
        <b-tr>
          <b-th>제목</b-th>
          <b-td>
            <b-form-input v-model="BE_title" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>내용</b-th>
          <b-td>
            <vue-editor id="responsibilityEdit" v-model="BE_content" :editorToolbar="customToolbar"></vue-editor>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>타입</b-th>
          <b-td>
            <b-form-select class="text-center" v-model="BE_type" :options="options2"></b-form-select>
          </b-td>
        </b-tr>
        <b-tr v-if="BE_type == '이동형'">
          <b-th>URL</b-th>
          <b-td>
            <b-form-input v-model="BE_url" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>시작일</b-th>
          <b-td>
            <b-form-datepicker v-model="BE_period_start"></b-form-datepicker>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>종료일</b-th>
          <b-td>
            <b-form-datepicker v-model="BE_period_end"></b-form-datepicker>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>주의사항</b-th>
          <b-td>
            <b-form-textarea v-model="BE_notice" rows="4" max-rows="6"></b-form-textarea>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>썸네일<br /><span id="img-set-font">*16:9 size</span></b-th>
          <b-td>
            <input type="file" accept="image/*" @change="uploadFile1($event)" id="admin-user-modal-file" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>이미지1</b-th>
          <b-td>
            <input type="file" accept="image/*" @change="uploadFile2($event)" id="admin-user-modal-file" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>이미지2</b-th>
          <b-td>
            <input type="file" accept="image/*" @change="uploadFile3($event)" id="admin-user-modal-file" />
          </b-td>
        </b-tr>
      </b-table-simple>
    </b-modal>

    <!-- 이벤트 수정 모달 -->
    <b-modal id="modal-event-edit" title="이벤트 수정" cancel-title="닫기" cancel-variant="danger" ok-title="수정" @ok="editEvent">
      <!-- <b-button
        size="sm"
        class="mb-2"
        variant="warning"
        @click="modalAddImgBtn"
      >
        이미지 추가
      </b-button> -->
      <b-table-simple id="user-modal-event">
        <b-tr>
          <b-th>제목</b-th>
          <b-td colspan="3">
            <b-form-input v-model="eventRowList.BE_title" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>내용</b-th>
          <b-td colspan="3">
            <vue-editor id="responsibilityEdit" v-model="eventRowList.BE_content" :editorToolbar="customToolbar"></vue-editor>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>타입</b-th>
          <b-td colspan="3">
            <b-form-select class="text-center" v-model="eventRowList.BE_type" :options="options2"></b-form-select>
          </b-td>
        </b-tr>
        <b-tr v-if="eventRowList.BE_type == '이동형'">
          <b-th>URL</b-th>
          <b-td colspan="3">
            <b-form-input v-model="eventRowList.BE_url" />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>시작일</b-th>
          <b-td colspan="3">
            <b-form-datepicker v-model="eventRowList.BE_period_start"></b-form-datepicker>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>종료일</b-th>
          <b-td colspan="3">
            <b-form-datepicker v-model="eventRowList.BE_period_end"></b-form-datepicker>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>주의사항</b-th>
          <b-td colspan="3">
            <b-form-textarea v-model="eventRowList.BE_notice" rows="4" max-rows="6"></b-form-textarea>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>썸네일<br /><span id="img-set-font">*16:9 size</span></b-th>
          <b-td style="width: 45%">
            <input type="file" accept="image/*" @change="uploadFileEdit1($event)" id="admin-user-modal-file" />
          </b-td>
          <b-td style="font-size: 14px">
            <a
              style="font-weight: bold"
              v-if="eventRowList.BE_path_img1 != ''"
              :href="eventRowList.BE_path_img1_1"
              onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
            >
              미리보기
            </a>
            <div v-else>-</div>
          </b-td>
          <b-td>
            <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg1"> 삭제 </b-form-checkbox>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>이미지1</b-th>
          <b-td style="width: 45%">
            <input type="file" accept="image/*" @change="uploadFileEdit2($event)" id="admin-user-modal-file" />
          </b-td>
          <b-td style="font-size: 14px">
            <a
              style="font-weight: bold"
              v-if="eventRowList.BE_path_img2 != ''"
              :href="eventRowList.BE_path_img2_1"
              onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
            >
              미리보기
            </a>
            <div v-else>-</div>
          </b-td>
          <b-td>
            <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg2"> 삭제 </b-form-checkbox>
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>이미지2</b-th>
          <b-td style="width: 45%">
            <input type="file" accept="image/*" @change="uploadFileEdit3($event)" id="admin-user-modal-file" />
          </b-td>
          <b-td style="font-size: 14px">
            <a
              style="font-weight: bold"
              v-if="eventRowList.BE_path_img3 != ''"
              :href="eventRowList.BE_path_img3_1"
              onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
            >
              미리보기
            </a>
            <div v-else>-</div>
          </b-td>
          <b-td>
            <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg3"> 삭제 </b-form-checkbox>
          </b-td>
        </b-tr>
      </b-table-simple>
    </b-modal>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      BE_type: "이동형",
      BE_title: "",
      BE_content: "",
      BE_url: "",
      BE_period_start: "",
      BE_period_end: "",
      BE_notice: "",
      BE_path_img1: "",
      BE_path_img2: "",
      BE_path_img3: "",
      BE_path_img1_edit: "",
      BE_path_img2_edit: "",
      BE_path_img3_edit: "",
      editorConfig: {},
      eventList: [],
      eventRowList: [],
      customToolbar: [["bold", "italic", "underline", { color: [] }]],
      fields: [
        { key: "BE_idx", label: "번호", thClass: "w5" },
        { key: "BE_type", label: "타입", thClass: "w7" },
        { key: "BE_title", label: "제목", thClass: "w25" },
        { key: "BE_period_start", label: "기간", thClass: "w20" },
        { key: "BE_path_img1", label: "썸네일", thClass: "w20" },
        "상세",
        "참가자",
        "삭제",
      ],
      options2: [
        { value: "이동형", text: "이동형" },
        { value: "참여형", text: "참여형" },
        { value: "공지형", text: "공지형" },
      ],
      options: [
        { value: "진행중인 이벤트", text: "진행중인 이벤트" },
        { value: "종료된 이벤트", text: "종료된 이벤트" },
      ],
      perPage: 8,
      filter: null,
      currentPage: 1,
      selectedItem: "진행중인 이벤트",
      keyword: "",
      modalAddImg1: false,
      modalAddImg2: false,
      delCheckImg1: "N",
      delCheckImg2: "N",
      delCheckImg3: "N",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getEventList();
  },
  computed: {
    rows() {
      return this.eventList.length;
    },
  },
  watch: {
    PM_category(value) {
      if (value == "URL") {
        this.PM_reference = "";
      } else if (value == "공지사항") {
        this.PM_reference = "공지사항";
      } else {
        this.PM_reference = "이벤트";
      }
    },
  },
  methods: {
    eventJoinList(item) {
      this.$router.push({
        name: "AdminUserEventJoinList",
        params: { idx: item.BE_idx },
      });
    },
    modalAddImgBtn() {
      if (this.modalAddImg1 == false) {
        this.modalAddImg1 = true;
      } else if (this.modalAddImg1 == true) {
        this.modalAddImg2 = true;
      }
    },
    uploadFile1(e) {
      this.BE_path_img1 = e.target.files[0];
    },
    uploadFile2(e) {
      this.BE_path_img2 = e.target.files[0];
    },
    uploadFile3(e) {
      this.BE_path_img3 = e.target.files[0];
    },
    uploadFileEdit1(e) {
      this.BE_path_img1_edit = e.target.files[0];
    },
    uploadFileEdit2(e) {
      this.BE_path_img2_edit = e.target.files[0];
    },
    uploadFileEdit3(e) {
      this.BE_path_img3_edit = e.target.files[0];
    },
    editModalBtn(item) {
      this.eventRowList = { ...item };
    },
    addEvent() {
      this.BE_title = "";
      this.BE_content = "";
      this.BE_url = "";
      this.BE_period_start = "";
      this.BE_period_end = "";
      this.BE_notice = "";
      this.BE_path_img1 = "";
      this.BE_path_img2 = "";
      this.BE_path_img3 = "";
      this.modalAddImg1 = false;
      this.modalAddImg2 = false;
    },
    async getEventList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/event/list`);
        this.eventList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.eventList.length; i++) {
          this.eventList[i].BE_path_img1_1 = `${this.$store.state.host}/${response.data[i].BE_path_img1}`;
          this.eventList[i].BE_path_img2_1 = `${this.$store.state.host}/${response.data[i].BE_path_img2}`;
          this.eventList[i].BE_path_img3_1 = `${this.$store.state.host}/${response.data[i].BE_path_img3}`;
        }
      } catch (err) {
        console.log(err);
      }
    },

    async getSearchEventList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/event/search`, {
          selectedItem: this.selectedItem,
          keyword: this.keyword,
        });
        this.eventList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.eventList.length; i++) {
          this.eventList[i].BE_path_img1_1 = `${this.$store.state.host}/${response.data[i].BE_path_img1}`;
          this.eventList[i].BE_path_img2_1 = `${this.$store.state.host}/${response.data[i].BE_path_img2}`;
          this.eventList[i].BE_path_img3_1 = `${this.$store.state.host}/${response.data[i].BE_path_img3}`;
        }
      } catch (err) {
        console.log(err);
      }
    },

    async saveEvent(bvModalEvent) {
      if (this.BE_title == "") {
        this.$swal("어랏!", "제목을 입력하세요.", "question");
        bvModalEvent.preventDefault();
      } else if (this.BE_path_img1 == "") {
        this.$swal("어랏!", "썸네일을 최소 한개 입력하세요.", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          const fd = new FormData();
          fd.append("BE_type", this.BE_type);
          fd.append("BE_title", this.BE_title);
          fd.append("BE_content", this.BE_content);
          fd.append("BE_url", this.BE_url);
          fd.append("BE_period_start", this.BE_period_start);
          fd.append("BE_period_end", this.BE_period_end);
          fd.append("BE_notice", this.BE_notice);
          fd.append("BE_path_img1", this.BE_path_img1);
          fd.append("BE_path_img2", this.BE_path_img2);
          fd.append("BE_path_img3", this.BE_path_img3);

          await this.$axios.post(`${this.$store.state.host}/admin/user/event/save`, fd);
          this.$swal("Success", "저장 되었습니다.", "success");
          this.BE_title = "";
          this.BE_content = "";
          this.BE_url = "";
          this.BE_period_start = "";
          this.BE_period_end = "";
          this.BE_notice = "";
          this.BE_path_img1 = "";
          this.BE_path_img2 = "";
          this.BE_path_img3 = "";
          this.getEventList();
        } catch (err) {
          console.log(err);
          this.$toast.error("실패했습니다.");
        }
      }
    },

    async editEvent() {
      try {
        const fd = new FormData();
        fd.append("BE_type", this.eventRowList.BE_type);
        fd.append("BE_title", this.eventRowList.BE_title);
        fd.append("BE_content", this.eventRowList.BE_content);
        fd.append("BE_url", this.eventRowList.BE_url);
        fd.append("BE_period_start", this.eventRowList.BE_period_start);
        fd.append("BE_period_end", this.eventRowList.BE_period_end);
        fd.append("BE_notice", this.eventRowList.BE_notice);
        fd.append("BE_path_img1", this.eventRowList.BE_path_img1);
        if (this.delCheckImg1 == "N") {
          fd.append("BE_path_img1_edit", this.BE_path_img1_edit);
        } else {
          fd.append("BE_path_img1_edit", "");
        }
        fd.append("BE_path_img2", this.eventRowList.BE_path_img2);
        if (this.delCheckImg2 == "N") {
          fd.append("BE_path_img2_edit", this.BE_path_img2_edit);
        } else {
          fd.append("BE_path_img2_edit", "");
        }
        fd.append("BE_path_img3", this.eventRowList.BE_path_img3);
        if (this.delCheckImg3 == "N") {
          fd.append("BE_path_img3_edit", this.BE_path_img3_edit);
        } else {
          fd.append("BE_path_img3_edit", "");
        }
        fd.append("delCheckImg1", this.delCheckImg1);
        fd.append("delCheckImg2", this.delCheckImg2);
        fd.append("delCheckImg3", this.delCheckImg3);

        await this.$axios.post(`${this.$store.state.host}/admin/user/event/edit/${this.eventRowList.BE_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");

        this.BE_path_img1_edit = "";
        this.BE_path_img2_edit = "";
        this.BE_path_img3_edit = "";
        this.delCheckImg1 = "";
        this.delCheckImg2 = "";
        this.delCheckImg3 = "";
        this.getEventList();
      } catch (err) {
        console.log(err);
        this.$toast.error("실패했습니다.");
      }
    },

    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/event/delete/${item.BE_idx}`, {
              BE_path_img1: item.BE_path_img1,
              BE_path_img2: item.BE_path_img2,
              BE_path_img3: item.BE_path_img3,
            });
            this.getEventList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
