<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 지원금 상한액 관리</span>
      </div>
      <div class="row ml-2 mt-1">
        <div class="row" id="admin-store-member-groupInput4">
          <div class="mr-1">
            <b-form-select v-model="selectedUsed" :options="options1"></b-form-select>
          </div>
          <div>
            <b-form-input placeholder="기기명 또는 모델명" v-model="keyword" @keyup.enter="getSearchSaleList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchSaleList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />
        <div v-if="isLoding == true">
          <b-table hover :items="phoneList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(BR_content)="row">
              <div id="admin-store-report-reason">
                {{ row.item.BR_content }}
              </div>
            </template>
            <template #cell(MD_grant_limit)="row">
              <div>
                <b-form-input size="sm" v-model="row.item.MD_grant_limit" />
              </div>
            </template>
            <template #cell(수정)="row">
              <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      phoneList: [],

      fields: [
        { key: "PS_name", label: "기기명", thClass: "w30" },
        {
          key: "MD_model",
          label: "모델명",
          thClass: "w30",
        },
        {
          key: "MD_release_price",
          label: "출고가",
          thClass: "w15",
          formatter: this.comma,
        },
        {
          key: "MD_grant_limit",
          label: "지원금 상한액",
          thClass: "w15",
        },
        "수정",
      ],
      options1: [
        { value: "1", text: "SK" },
        { value: "2", text: "KT" },
        { value: "3", text: "LG" },
        { value: "4", text: "7M" },
        { value: "5", text: "MM" },
        { value: "6", text: "UM" },
        { value: "7", text: "HEL_SK" },
        { value: "8", text: "HEL_KT" },
        { value: "9", text: "S1_SK" },
        { value: "10", text: "S1_KT" },
        { value: "11", text: "SKB" },
        { value: "12", text: "HEL_LG" },
        { value: "13", text: "HEL" },
        { value: "14", text: "기타" },
      ],

      selectedUsed: "1",
      keyword: "",
      perPage: 13,
      filter: null,
      currentPage: 1,
      modalMemo: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getStoreSaleList();
  },
  computed: {
    rows() {
      return this.phoneList.length;
    },
  },
  methods: {
    async getStoreSaleList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/sale/list`);
        this.phoneList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    async getSearchSaleList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/sale/search`, {
          selectedUsed: this.selectedUsed,
          keyword: this.keyword,
        });
        this.phoneList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    comma(value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    async editBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/sale/edit`, {
          MD_grant_limit: item.MD_grant_limit,
          MD_idx: item.MD_idx,
        });
        if (response.status == 200) {
          this.$swal("Success", "수정되었습니다.", "success");
          this.getStoreSaleList();
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
