<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 이벤트 참여자 조회({{ title }}) </span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn3" @click="goEventList"> 이벤트 목록보기 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInpu9">
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getSearchList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table :items="eventJoinList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
          <template #cell(BEP_idx)="data">
            {{ data.index + 1 }}
          </template>

          <template #cell(BEP_email)="row">
            {{ row.item.BEP_email }}
          </template>
          <template #cell(BEP_regdate)="row">
            {{ row.item.BEP_regdate | yyyyMMddhhmmss }}
          </template>
        </b-table>
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      eventJoinList: [],
      perPage: 8,
      currentPage: 1,
      keyword: "",
      fields: [
        { key: "BEP_idx", label: "번호", thClass: "w10" },
        { key: "BEP_name", label: "닉네임", thClass: "w30" },
        { key: "BEP_email", label: "이메일", thClass: "w30" },
        { key: "BEP_regdate", label: "참여시각", thClass: "w30" },
      ],
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getEventList();
  },
  computed: {
    rows() {
      return this.eventJoinList.length;
    },
  },

  methods: {
    async getEventList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/event/joinlist/${this.$route.params.idx}`);
        this.eventJoinList = response.data;
        this.title = response.data[0].BE_title;
      } catch (err) {
        console.log(err);
      }
    },

    async getSearchList() {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/event/joinlist/search`, {
          idx: this.$route.params.idx,
          keyword: this.keyword,
        });
        this.eventJoinList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    goEventList() {
      this.$router.push("/admin/user/event");
    },
  },
};
</script>

<style></style>
