<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 회원탈퇴관리</span>
      </div>
      <div class="row ml-2 mt-1">
        <div class="row" id="admin-store-member-groupInput7">
          <div class="mr-1">
            <b-form-select v-model="selectedReason" :options="options1"></b-form-select>
          </div>
          <div class="mr-1">
            <b-form-select v-model="selectedID" :options="options2"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getUserSearchMemberOutList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getUserSearchMemberOutList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="memberOutList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(MO_email)="row">
              {{ row.item.MO_email }}
            </template>
            <template #cell(MO_reason)="row">
              <div v-if="row.item.MO_reason == '1'">더이상 이용을 안해요</div>
              <div v-if="row.item.MO_reason == '2'">딜의 결과가 마음에 들지 않아요</div>
              <div v-if="row.item.MO_reason == '3'">고객 응대가 마음에 들지 않아요</div>
              <div v-if="row.item.MO_reason == '4'">다른 서비스를 이용하려고 해요</div>
              <div v-if="row.item.MO_reason == '5'">기타</div>
              <div v-if="row.item.MO_reason == '6'">웹에서 회원 탈퇴</div>
            </template>
            <template #cell(MO_regdate)="row">
              <div>{{ row.item.MO_regdate | yyyyMMdd }}</div>
            </template>
            <template #cell(MO_out_date)="row">
              <div>{{ row.item.MO_out_date | yyyyMMdd }}</div>
            </template>
          </b-table>
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      memberOutList: [],
      fields: [
        {
          key: "MO_email",
          label: "이메일",
          thClass: "w15",
        },
        {
          key: "MO_name",
          label: "닉네임",
          thClass: "w20",
        },
        { key: "MO_reason", label: "탈퇴사유", thClass: "w20" },
        { key: "MO_reason_content", label: "상세이유", thClass: "w25" },
        { key: "MO_regdate", label: "가입일", thClass: "w10" },
        { key: "MO_out_date", label: "탈퇴일", thClass: "w10" },
      ],
      options1: [
        { value: "", text: "전체" },
        { value: "1", text: "더이상 이용을 안해요" },
        { value: "2", text: "딜의 결과가 마음에 들지 않아요" },
        { value: "3", text: "고객 응대가 마음에 들지 않아요" },
        { value: "4", text: "다른 서비스를 이용하려고 해요" },
        { value: "5", text: "기타" },
        { value: "6", text: "웹에서 회원 탈퇴" },
      ],
      options2: [
        { value: "닉네임", text: "닉네임" },
        { value: "이메일", text: "이메일" },
      ],

      selectedReason: "",
      selectedID: "닉네임",
      keyword: "",
      perPage: 16,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getUserMemberOutList();
  },
  computed: {
    rows() {
      return this.memberOutList.length;
    },
  },
  methods: {
    async getUserMemberOutList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/memberout/list`);
        this.memberOutList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    async getUserSearchMemberOutList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/memberout/search`, {
          selectedReason: this.selectedReason,
          selectedID: this.selectedID,
          keyword: this.keyword,
        });
        this.memberOutList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
