<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 이용약관 관리 </span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" @click="saveTerms"> 저장 </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-user-terms-table">
          <b-tr>
            <b-th> 서비스<br />이용약관 </b-th>
            <b-td>
              <vue-editor id="admin-vue-editor" v-model="terms.T_service" :editorToolbar="customToolbar"> </vue-editor>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>개인정보<br />취급방침</b-th>
            <b-td>
              <vue-editor id="admin-vue-editor" v-model="terms.T_privacy" :editorToolbar="customToolbar"> </vue-editor>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>위치기반<br />서비스 이용약관</b-th>
            <b-td>
              <vue-editor id="admin-vue-editor" v-model="terms.T_location" :editorToolbar="customToolbar"> </vue-editor>
            </b-td>
          </b-tr>
        </b-table-simple>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      terms: [],
      customToolbar: [["bold", "italic", "underline", { color: [] }]],
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getTerms();
  },
  methods: {
    async getTerms() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/terms/list`);
        this.terms = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async saveTerms() {
      try {
        await this.$axios.post(`${this.$store.state.host}/admin/user/terms/edit`, {
          T_service: this.terms.T_service,
          T_privacy: this.terms.T_privacy,
          T_location: this.terms.T_location,
          T_idx: this.terms.T_idx,
        });
        this.$swal("Success", "수정 되었습니다.", "success");
        this.getTerms();
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

