<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 메인 팝업 관리 </span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" v-b-modal.modal-1> 추가 </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table hover :items="popupMainList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
          <template #cell(PM_path_img2)="row">
            <b-img id="admin-user-popup-img" :src="row.item.PM_path_img2" />
          </template>

          <template #cell(수정)="row">
            <b-button size="sm" v-b-modal.modal-2 variant="primary" @click="editModalBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
          </template>
          <template #cell(삭제)="row">
            <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
      <b-modal id="modal-1" title="메인 팝업 추가" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="savePopupMain">
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th style="width: 30%">순서</b-th>
            <b-td>
              <b-form-input v-model="PM_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td>
              <b-form-input v-model="PM_title" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>유형</b-th>
            <b-td>
              <b-form-select v-model="PM_category" :options="options"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr v-if="PM_category == 'URL'">
            <b-th>URL</b-th>
            <b-td>
              <b-form-input v-model="PM_url" />
            </b-td>
          </b-tr>
          <b-tr v-if="PM_category == '이벤트'">
            <b-th>이벤트</b-th>
            <b-td>
              <b-form-input v-model="PM_event" />
            </b-td>
          </b-tr>
          <b-tr v-if="PM_category == '공지사항'">
            <b-th>공지사항</b-th>
            <b-td>
              <b-form-input v-model="PM_notice" />
            </b-td>
          </b-tr>
          <b-tr v-if="PM_category == '이미지'">
            <b-th>이미지</b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile2($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>팝업이미지<br /><span id="img-set-font">*16:9 size</span></b-th>
            <b-td>
              <input type="file" accept="image/*" @change="uploadFile1($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
      <b-modal
        id="modal-2"
        title="메인 팝업 수정"
        cancel-title="닫기"
        cancel-variant="danger"
        ok-title="수정"
        @ok="editPopupMain"
        @cancel="cancelModal"
      >
        <b-table-simple id="store-modal">
          <b-tr>
            <b-th style="width: 30%">순서</b-th>
            <b-td colspan="2">
              <b-form-input v-model="popupRowMainList.PM_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td colspan="2">
              <b-form-input v-model="popupRowMainList.PM_title" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>유형</b-th>
            <b-td colspan="2">
              <b-form-select v-model="popupRowMainList.PM_category" :options="options"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr v-if="popupRowMainList.PM_category == 'URL'">
            <b-th>URL</b-th>
            <b-td colspan="2">
              <b-form-input v-model="popupRowMainList.PM_url" />
            </b-td>
          </b-tr>
          <b-tr v-if="popupRowMainList.PM_category == '이벤트'">
            <b-th>이벤트</b-th>
            <b-td colspan="2">
              <b-form-input v-model="popupRowMainList.PM_event" />
            </b-td>
          </b-tr>
          <b-tr v-if="popupRowMainList.PM_category == '공지사항'">
            <b-th>공지사항</b-th>
            <b-td colspan="2">
              <b-form-input v-model="popupRowMainList.PM_notice" />
            </b-td>
          </b-tr>
          <b-tr v-if="popupRowMainList.PM_category == '이미지'">
            <b-th>이미지</b-th>
            <b-td style="width: 55%">
              <input type="file" accept="image/*" @change="uploadEditFile2($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td v-if="popupRowMainList.PM_img_path_img != ''">
              <a :href="popupRowMainList.PM_img_path_img2" onclick="window.open(this.href, '_blank', 'width=100%'); return false;">
                미리 보기
              </a>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>팝업이미지<br /><span id="img-set-font">*16:9 size</span></b-th>
            <b-td style="width: 55%">
              <input type="file" accept="image/*" @change="uploadEditFile1($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td v-if="popupRowMainList.PM_path_img != ''">
              <a :href="popupRowMainList.PM_path_img2" onclick="window.open(this.href, '_blank', 'width=100%'); return false;">
                미리 보기
              </a>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      PM_order: "",
      PM_title: "",
      PM_category: "URL",
      PM_url: "",
      PM_event: "",
      PM_notice: "",
      PM_path_img: "",
      PM_path_img_edit: "",
      PM_img_path_img: "",
      PM_img_path_img_edit: "",
      popupMainList: [],
      popupRowMainList: [],
      fields: [
        { key: "PM_order", label: "순서", thClass: "w10" },
        { key: "PM_title", label: "제목", thClass: "w30" },
        { key: "PM_category", label: "유형", thClass: "w20" },
        { key: "PM_path_img2", label: "썸네일", thClass: "w20" },
        "수정",
        "삭제",
      ],
      options: [
        { value: "URL", text: "URL" },
        { value: "공지사항", text: "공지사항" },
        { value: "이벤트", text: "이벤트" },
        { value: "이미지", text: "이미지" },
      ],
      perPage: 6,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getPopupMainList();
  },
  computed: {
    rows() {
      return this.popupMainList.length;
    },
  },
  watch: {
    PM_category(value) {
      if (value == "URL") {
        this.PM_event = "";
        this.PM_notice = "";
        this.PM_img_path_img = "";
      } else if (value == "공지사항") {
        this.PM_event = "";
        this.PM_url = "";
        this.PM_img_path_img = "";
      } else if (value == "이벤트") {
        this.PM_url = "";
        this.PM_notice = "";
        this.PM_img_path_img = "";
      } else {
        this.PM_event = "";
        this.PM_notice = "";
        this.PM_url = "";
      }
    },
  },
  methods: {
    uploadFile1(e) {
      this.PM_path_img = e.target.files[0];
    },
    uploadFile2(e) {
      this.PM_img_path_img = e.target.files[0];
    },
    uploadEditFile1(e) {
      this.PM_path_img_edit = e.target.files[0];
    },
    uploadEditFile2(e) {
      this.PM_img_path_img_edit = e.target.files[0];
    },
    editModalBtn(item) {
      this.popupRowMainList = { ...item };
    },
    cancelModal() {
      this.popupRowMainList = [];
    },
    async getPopupMainList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/popupmain`);
        this.popupMainList = response.data;
        for (var i = 0; i < this.popupMainList.length; i++) {
          this.popupMainList[i].PM_path_img2 = `${this.$store.state.host}/${response.data[i].PM_path_img}`;
        }
      } catch (err) {
        console.log(err);
      }
    },

    async savePopupMain(bvModalEvent) {
      if (this.PM_path_img == "") {
        this.$swal("어랏!", "팝업이미지를 입력해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        if (this.PM_category == "URL" && this.PM_url == "") {
          this.$swal("어랏!", "URL 정보를 입력해주세요", "question");
          bvModalEvent.preventDefault();
        } else if (this.PM_category == "공지사항" && this.PM_notice == "") {
          this.$swal("어랏!", "공지사항 정보를 입력해주세요", "question");
          bvModalEvent.preventDefault();
        } else if (this.PM_category == "이벤트" && this.PM_event == "") {
          this.$swal("어랏!", "이벤트 정보를 입력해주세요", "question");
          bvModalEvent.preventDefault();
        } else if (this.PM_category == "이미지" && this.PM_img_path_img == "") {
          this.$swal("어랏!", "이미지를 입력해주세요", "question");
          bvModalEvent.preventDefault();
        } else {
          const fd = new FormData();
          fd.append("PM_order", this.PM_order);
          fd.append("PM_title", this.PM_title);
          fd.append("PM_category", this.PM_category);
          fd.append("PM_path_img", this.PM_path_img);

          if (this.PM_category == "URL") {
            fd.append("PM_url", this.PM_url);
          }
          if (this.PM_category == "공지사항") {
            fd.append("PM_notice", this.PM_notice);
          }
          if (this.PM_category == "이미지") {
            fd.append("PM_img_path_img", this.PM_img_path_img);
          }

          await this.$axios.post(`${this.$store.state.host}/admin/user/popupmain/create`, fd);
          this.$swal("Success", "저장 되었습니다.", "success");
          this.PM_order = "";
          this.PM_title = "";
          this.PM_category = "";
          this.PM_url = "";
          this.PM_notice = "";
          this.PM_img_path_img = "";
          this.PM_path_img = "";
          this.getPopupMainList();
        }
      }
    },

    async editPopupMain() {
      try {
        console.log(this.popupRowMainList);
        const fd = new FormData();
        fd.append("PM_order", this.popupRowMainList.PM_order);
        fd.append("PM_title", this.popupRowMainList.PM_title);
        fd.append("PM_category", this.popupRowMainList.PM_category);

        if (this.popupRowMainList.PM_category == "URL") {
          fd.append("PM_path_img", this.popupRowMainList.PM_path_img);
          fd.append("PM_path_img_edit", this.PM_path_img_edit);

          fd.append("PM_url", this.popupRowMainList.PM_url);
          fd.append("PM_img_path_img", "");
          fd.append("PM_event", "");
          fd.append("PM_notice", "");
        }

        if (this.popupRowMainList.PM_category == "이미지") {
          fd.append("PM_path_img", this.popupRowMainList.PM_path_img);
          fd.append("PM_path_img_edit", this.PM_path_img_edit);

          fd.append("PM_url", "");
          fd.append("PM_img_path_img", this.popupRowMainList.PM_img_path_img);
          fd.append("PM_img_path_img_edit", this.PM_img_path_img_edit);
          fd.append("PM_event", "");
          fd.append("PM_notice", "");
        }

        if (this.popupRowMainList.PM_category == "이벤트") {
          fd.append("PM_path_img", this.popupRowMainList.PM_path_img);
          fd.append("PM_path_img_edit", this.PM_path_img_edit);

          fd.append("PM_url", "");
          fd.append("PM_img_path_img", "");
          fd.append("PM_event", this.popupRowMainList.PM_event);
          fd.append("PM_notice", "");
        }

        if (this.popupRowMainList.PM_category == "공지사항") {
          fd.append("PM_path_img", this.popupRowMainList.PM_path_img);
          fd.append("PM_path_img_edit", this.PM_path_img_edit);

          fd.append("PM_url", "");
          fd.append("PM_img_path_img", "");
          fd.append("PM_event", "");
          fd.append("PM_notice", this.popupRowMainList.PM_notice);
        }

        await this.$axios.post(`${this.$store.state.host}/admin/user/popupmain/edit/${this.popupRowMainList.PM_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.PM_path_img_edit = "";
        this.PM_img_path_img_edit = "";
        this.getPopupMainList();
      } catch (err) {
        console.log(err);
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/popupmain/delete/${item.PM_idx}`, {
              PM_path_img: item.PM_path_img,
              PM_img_path_img: item.PM_img_path_img,
            });
            this.getPopupMainList();
            this.$toast.success("삭제되었습니다");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
  },
};
</script>

<style></style>
