<template>
  <div class="admin-login">
    <div id="adminmain-login-title">
      <span id="admin-main-login-font">BANANA DEAL</span>
    </div>
    <div id="adminmain-login-box">
      <div id="adminmain-login-subtitle">
        <img src="@/assets/main_img/banana2.png" class="rounded" alt="MAINLOGO" id="admin-side-mainImg" />로그인 해주세요
      </div>
      <b-input v-model="AM_id" placeholder="ID" id="admin-member-id-input-form1"></b-input>
      <b-input v-model="AM_pw" type="password" placeholder="PASSWORD" id="admin-member-id-input-form2" @keyup.enter="goAdminPage"></b-input>
      <b-button variant="success" id="adminmain-login-btn" @click="goAdminPage">로그인</b-button>
    </div>
    <div id="adminmain-login-back-color"></div>
    <div id="adminmain-login-back-color2">
      <img src="@/assets/main_img/monkey.png" class="rounded" alt="MAINLOGO" id="admin-login-monkey" />
    </div>
  </div>
</template>

<script>
import { saveAuthToCookie } from "@/utils/cookies";
import { Buffer } from "buffer";
export default {
  data() {
    return {
      AM_id: "",
      AM_pw: "",
    };
  },
  methods: {
    async goAdminPage() {
      try {
        if (this.AM_id == "") {
          this.$swal("어랏!", "이메일을 입력해주세요", "question");
        } else if (this.AM_pw == "") {
          this.$swal("어랏!", "비밀번호를 입력해주세요", "question");
        } else {
          const response = await this.$axios.post(`${this.$store.state.host}/admin/login`, {
            AM_id: this.AM_id,
            AM_pw: Buffer.from(`${this.AM_pw}`, "utf8").toString("base64"),
          });
          if (response.data.message == "아이디 틀림") {
            this.$swal("Error", "계정를 다시 확인 해주세요.", "error");
          } else if (response.data.message == "비밀번호 틀림") {
            this.$swal("Error", "비밀번호를 다시 확인 해주세요.", "error");
          } else {
            this.$store.commit("userStore/login", response.data.data);
            saveAuthToCookie(response.data.token);
            this.$router.push("/admin/home");
            this.$swal(
              this.$store.state.userStore.AM_name + this.$store.state.userStore.AM_rank + "님",
              "오늘 하루도 화이팅입니다!!!",
              "success"
            );
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
