<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 자주하는 질문</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button variant="warning" id="admin-user-top-btn" v-b-modal.modal-fav1 @click="addFavorite"> 추가 </b-button>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="favoriteList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(BFQ_ask)="row">
              <div id="admin-user-fav-textline">{{ row.item.BFQ_ask }}</div>
            </template>
            <template #cell(BFQ_answer)="row">
              <div id="admin-user-fav-textline">{{ row.item.BFQ_answer }}</div>
            </template>
            <template #cell(상세보기)="row">
              <b-button size="sm" variant="success" v-b-modal.modal-fav2 @click="editBtn(row.item)" id="board_edit_btn">
                상세보기
              </b-button>
            </template>
            <template #cell(삭제)="row">
              <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>

      <!-- 추가 모달 -->
      <b-modal id="modal-fav1" title="자주하는 질문 추가" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="saveFavorite">
        <b-table-simple id="user-modal-inq">
          <b-tr>
            <b-th>순서</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="BFQ_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>질문</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="BFQ_ask" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변</b-th>
            <b-td colspan="4">
              <b-form-textarea v-model="BFQ_answer" rows="4" max-rows="6"></b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지1</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile1($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지2</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile2($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지3</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile3($event)" id="admin-user-modal-file" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>URL1</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="BFQ_url1" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>URL2</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="BFQ_url2" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
      <!-- 상세 모달 부분 -->
      <b-modal id="modal-fav2" title="자주하는 질문 수정" cancel-title="닫기" cancel-variant="danger" ok-title="수정" @ok="editFavorite">
        <b-table-simple id="user-modal-inq">
          <b-tr>
            <b-th>순서</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="selectRowItem.BFQ_order" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>질문</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="selectRowItem.BFQ_ask" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변</b-th>
            <b-td colspan="4">
              <b-form-textarea v-model="selectRowItem.BFQ_answer" rows="4" max-rows="6"></b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지1</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFileEdit1($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td style="width: 20%">
              <a
                v-if="selectRowItem.BFQ_path_img1 != ''"
                :href="selectRowItem.BFQ_path_img1_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td style="width: 15%">
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg1"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지2</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFileEdit2($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td style="width: 20%">
              <a
                v-if="selectRowItem.BFQ_path_img2 != ''"
                :href="selectRowItem.BFQ_path_img2_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td style="width: 15%">
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg2"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지3</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFileEdit3($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td style="width: 20%">
              <a
                v-if="selectRowItem.BFQ_path_img3 != ''"
                :href="selectRowItem.BFQ_path_img3_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td style="width: 15%">
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg3"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>URL1</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="selectRowItem.BFQ_url1" />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>URL2</b-th>
            <b-td colspan="4">
              <b-form-input size="sm" v-model="selectRowItem.BFQ_url2" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      favoriteList: [],
      selectRowItem: [],
      fields: [
        { key: "BFQ_order", label: "순서", thClass: "w10" },
        {
          key: "BFQ_ask",
          label: "질문",
          thClass: "w30",
        },
        {
          key: "BFQ_answer",
          label: "답변",
          thClass: "w40",
        },
        "상세보기",
        "삭제",
      ],
      perPage: 10,
      currentPage: 1,
      delCheckImg1: "N",
      delCheckImg2: "N",
      delCheckImg3: "N",
      BFQ_path_img1_edit: "",
      BFQ_path_img2_edit: "",
      BFQ_path_img3_edit: "",
      BFQ_order: "",
      BFQ_ask: "",
      BFQ_answer: "",
      BFQ_path_img1: "",
      BFQ_path_img2: "",
      BFQ_path_img3: "",
      BFQ_url1: "",
      BFQ_url2: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.getFavoriteList();
  },
  computed: {
    rows() {
      return this.favoriteList.length;
    },
  },
  methods: {
    uploadFile1(e) {
      this.BFQ_path_img1 = e.target.files[0];
    },
    uploadFile2(e) {
      this.BFQ_path_img2 = e.target.files[0];
    },
    uploadFile3(e) {
      this.BFQ_path_img3 = e.target.files[0];
    },
    uploadFileEdit1(e) {
      this.BFQ_path_img1_edit = e.target.files[0];
    },
    uploadFileEdit2(e) {
      this.BFQ_path_img2_edit = e.target.files[0];
    },
    uploadFileEdit3(e) {
      this.BFQ_path_img3_edit = e.target.files[0];
    },

    async getFavoriteList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/favorite/list`);
        this.favoriteList = response.data;
        this.isLoding = true;
        for (var i = 0; i < this.favoriteList.length; i++) {
          this.favoriteList[i].BFQ_path_img1_1 = `${this.$store.state.host}/${response.data[i].BFQ_path_img1}`;
          this.favoriteList[i].BFQ_path_img2_1 = `${this.$store.state.host}/${response.data[i].BFQ_path_img2}`;
          this.favoriteList[i].BFQ_path_img3_1 = `${this.$store.state.host}/${response.data[i].BFQ_path_img3}`;
        }
      } catch (err) {
        console.log(err);
      }
    },
    editBtn(item) {
      this.selectRowItem = { ...item };
    },
    addFavorite() {
      this.BFQ_order = "";
      this.BFQ_ask = "";
      this.BFQ_answer = "";
      this.BFQ_path_img1 = "";
      this.BFQ_path_img2 = "";
      this.BFQ_path_img3 = "";
    },

    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/favorite/delete/${item.BFQ_idx}`, {
              BFQ_path_img1: item.BFQ_path_img1,
              BFQ_path_img2: item.BFQ_path_img2,
              BFQ_path_img3: item.BFQ_path_img3,
            });
            this.getFavoriteList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
    async saveFavorite(bvModalEvent) {
      if (this.BFQ_ask == "") {
        this.$swal("어랏!", "질문을 작성해주세요", "question");
        bvModalEvent.preventDefault();
      } else if (this.BFQ_answer == "") {
        this.$swal("어랏!", "답변을 작성해주세요", "question");
        bvModalEvent.preventDefault();
      } else {
        try {
          const fd = new FormData();
          fd.append("BFQ_order", this.BFQ_order);
          fd.append("BFQ_ask", this.BFQ_ask);
          fd.append("BFQ_answer", this.BFQ_answer);
          fd.append("BFQ_path_img1", this.BFQ_path_img1);
          fd.append("BFQ_path_img2", this.BFQ_path_img2);
          fd.append("BFQ_path_img3", this.BFQ_path_img3);
          fd.append("BFQ_url1", this.BFQ_url1);
          fd.append("BFQ_url2", this.BFQ_url2);

          await this.$axios.post(`${this.$store.state.host}/admin/user/favorite/create`, fd);
          this.$swal("Success", "저장 되었습니다.", "success");
          this.BFQ_order = "";
          this.BFQ_ask = "";
          this.BFQ_answer = "";
          this.BFQ_path_img3 = "";
          this.BFQ_path_img3 = "";
          this.BFQ_path_img3 = "";
          this.BFQ_url1 = "";
          this.BFQ_url2 = "";
          this.getFavoriteList();
        } catch (err) {
          console.log(err);
          this.$toast.error("실패했습니다.");
        }
      }
    },
    async editFavorite() {
      try {
        const fd = new FormData();
        fd.append("BFQ_order", this.selectRowItem.BFQ_order);
        fd.append("BFQ_ask", this.selectRowItem.BFQ_ask);
        fd.append("BFQ_answer", this.selectRowItem.BFQ_answer);
        fd.append("BFQ_url1", this.selectRowItem.BFQ_url1);
        fd.append("BFQ_url2", this.selectRowItem.BFQ_url2);

        fd.append("BFQ_path_img1", this.selectRowItem.BFQ_path_img1);
        if (this.delCheckImg1 == "N") {
          fd.append("BFQ_path_img1_edit", this.BFQ_path_img1_edit);
        } else {
          fd.append("BFQ_path_img1_edit", "");
        }

        fd.append("BFQ_path_img2", this.selectRowItem.BFQ_path_img2);
        if (this.delCheckImg2 == "N") {
          fd.append("BFQ_path_img2_edit", this.BFQ_path_img2_edit);
        } else {
          fd.append("BFQ_path_img2_edit", "");
        }

        fd.append("BFQ_path_img3", this.selectRowItem.BFQ_path_img3);
        if (this.delCheckImg3 == "N") {
          fd.append("BFQ_path_img3_edit", this.BFQ_path_img3_edit);
        } else {
          fd.append("BFQ_path_img3_edit", "");
        }

        fd.append("delCheckImg1", this.delCheckImg1);
        fd.append("delCheckImg2", this.delCheckImg2);
        fd.append("delCheckImg3", this.delCheckImg3);

        await this.$axios.post(`${this.$store.state.host}/admin/user/favorite/edit/${this.selectRowItem.BFQ_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.BFQ_path_img1_edit = "";
        this.BFQ_path_img2_edit = "";
        this.BFQ_path_img3_edit = "";
        this.delCheckImg1 = "N";
        this.delCheckImg2 = "N";
        this.delCheckImg3 = "N";
        this.getFavoriteList();
      } catch (err) {
        console.log(err);
        this.$toast.error("실패했습니다.");
      }
    },
  },
};
</script>

<style></style>
