<template>
  <div>
    <div id="link-title">
      <img src="@/assets/main_img/banana_link.png" class="rounded" alt="MAINLOGO" id="app-link-monkey" />
      <b-button id="link_btn" variant="warning" @click="redirectToAppOrStore">이동</b-button>
    </div>
  </div>
</template>
<script src="https://cdn.jsdelivr.net/npm/platform@1.3.6"></script>
<script>
export default {
  mounted() {
    const id = this.$route.params.id;
    const idx = this.$route.params.idx;
    let appUrl = `bananadeal://${id}/${idx}`;
    let androidStoreUrl = "https://play.google.com/store/apps";
    let appleStoreUrl = "https://apps.apple.com/kr/app/apple-store/id375380948";

    var userAgent = navigator.userAgent;
    var visitedAt = new Date().getTime(); // 방문 시간

    if (userAgent.match(/iPhone|iPad|iPod/)) {
      setTimeout(function () {
        if (new Date().getTime() - visitedAt < 2000) {
          location.href = appleStoreUrl;
        }
      }, 500);
      setTimeout(function () {
        location.href = appUrl;
      }, 0);
    } else if (userAgent.match(/android/)) {
      // 안드로이드에서 실행 중
      setTimeout(function () {
        if (new Date().getTime() - visitedAt < 2000) {
          location.href = androidStoreUrl;
        }
      }, 500);

      setTimeout(function () {
        location.href = appUrl;
      }, 0);
    } else {
      // 모바일 기기가 아닌 경우
      window.location.href = appUrl;
    }
  },
  methods: {
    redirectToAppOrStore() {
      const id = this.$route.params.id;
      const idx = this.$route.params.idx;
      let appUrl = `bananadeal://${id}/${idx}`;
      let androidStoreUrl = "https://play.google.com/store/apps";
      let appleStoreUrl = "https://apps.apple.com/kr/app/apple-store/id375380948";

      var userAgent = navigator.userAgent;
      var visitedAt = new Date().getTime(); // 방문 시간

      if (userAgent.match(/iPhone|iPad|iPod/)) {
        setTimeout(function () {
          if (new Date().getTime() - visitedAt < 6000) {
            location.href = appleStoreUrl;
          }
        }, 500);
        setTimeout(function () {
          location.href = appUrl;
        }, 0);
      } else if (userAgent.match(/android/)) {
        // 안드로이드에서 실행 중
        setTimeout(function () {
          if (new Date().getTime() - visitedAt < 2000) {
            location.href = androidStoreUrl;
          }
        }, 500);

        setTimeout(function () {
          location.href = appUrl;
        }, 0);
      } else {
        // 모바일 기기가 아닌 경우
        window.location.href = appUrl;
      }
    },
  },
};
</script>

<style>
#app-link-monkey {
  width: 100%;
}
#link-title {
  font-size: 30px;
  color: #754600;
  font-family: "MonoplexKR-Regular";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#link_btn {
  margin-top: 30px;
  width: 100%;
}
</style>
