<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 견적 리스트 보기</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button v-if="$store.state.userStore.AM_rank == '관리자'" id="admin-store-member-topBtn3" @click="goDealList">
            견적 딜리스트
          </b-button>
          <span id="admin-deal-top-deal-info" v-if="estimateList != ''">
            현재 딜정보 : {{ estimateList.length }} ( {{ estimateList[0].DI_join_type }} / {{ estimateList[0].DI_before_tk_idx | tk_idx }} /
            {{ estimateList[0].DI_after_tk_idx | tk_idx }} / {{ estimateList[0].DI_hope_phone }} / {{ estimateList[0].DI_age_type }} /
            {{ estimateList[0].DI_reg_user }} )
          </span>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table
            hover
            :items="estimateList"
            :per-page="perPage"
            :current-page="currentPage"
            :fields="fields"
            id="admin-dealestimate-table"
          >
            <template #cell(D_status)="row">
              <div v-if="row.item.D_status == 'NEW'">NEW</div>
              <div v-if="row.item.D_status == 'READ'">열람</div>
              <div v-if="row.item.D_status == 'PARTICIPATE'">참여</div>
              <div v-if="row.item.D_status == 'ACCEPT'">수락</div>
              <div v-if="row.item.D_status == 'OPEN'">개통</div>
              <div v-if="row.item.D_status == 'END'">종료</div>
            </template>
            <template #cell(DE_join_tk_idx)="row">
              {{ row.item.DE_join_tk_idx | tk_idx }}
            </template>
            <template #cell(DE_factory_price)="row">
              {{ row.item.DE_factory_price | comma }}
            </template>
            <template #cell(DE_support_price)="row">
              {{ row.item.DE_support_price | comma }}
            </template>
            <template #cell(DE_sale)="row">
              {{ row.item.DE_sale | comma }}
            </template>
          </b-table>
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      estimateList: [],
      fields: [
        { key: "DE_idx", label: "번호", thClass: "w5" },
        { key: "D_status", label: "상태", thClass: "w5" },
        { key: "DE_type", label: "유형", thClass: "w5" },
        { key: "DE_join_tk_idx", label: "개통통신사", thClass: "w7" },
        { key: "DE_ps_name", label: "단말기", thClass: "w15" },
        { key: "DE_factory_price", label: "출고가", thClass: "w5" },
        { key: "DE_support_price", label: "공시지원금", thClass: "w7" },
        { key: "DE_plan", label: "요금제", thClass: "w10" },
        { key: "DE_installment_month", label: "할부개월", thClass: "w7" },
        { key: "DE_support_type", label: "지원유형", thClass: "w7" },
        { key: "DE_sale", label: "기타할인", thClass: "w7" },
        { key: "DE_suggestions", label: "제안사항" },
      ],
      perPage: 20,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getDealEstimateList();
  },
  computed: {
    rows() {
      return this.estimateList.length;
    },
  },
  methods: {
    async getDealEstimateList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/share/estimate/${this.$route.params.id}`);
        this.estimateList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    goDealList() {
      this.$router.push("/admin/share/deal").catch(() => {});
    },
  },
};
</script>

<style></style>
