<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 1 : 1 문의</span>
      </div>
      <div class="row ml-2 mt-1">
        <div class="row" id="admin-store-member-groupInput12">
          <div class="mr-1">
            <b-form-select v-model="selectedStatus" :options="options1"></b-form-select>
          </div>
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getSearchInqList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchInqList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="memberInqList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(MI_q_regdate)="row">
              {{ row.item.MI_q_regdate | yyyyMMdd }}
            </template>
            <template #cell(상세보기)="row">
              <b-button size="sm" variant="success" v-b-modal.modal-inq @click="editBtn(row.item)" id="board_edit_btn"> 상세보기 </b-button>
            </template>
            <template #cell(삭제)="row">
              <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>

      <!-- 모달 부분 -->
      <b-modal id="modal-inq" title="1:1 문의 상세보기" cancel-title="닫기" cancel-variant="danger" ok-title="저장" @ok="saveInq">
        <b-table-simple id="user-modal-inq">
          <!-- <b-button
            size="sm"
            class="mb-2"
            variant="warning"
            @click="modalAddImg"
          >
            답변이미지 추가
          </b-button> -->
          <b-tr>
            <b-th>처리상태</b-th>
            <b-td colspan="4">
              <b-form-select size="sm" v-model="selectRowItem.MI_status" :options="options1"></b-form-select>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>작성자</b-th>
            <b-td colspan="4">{{ selectRowItem.MI_name }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>제목</b-th>
            <b-td colspan="4">{{ selectRowItem.MI_title }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>내용</b-th>
            <b-td colspan="4">{{ selectRowItem.MI_content }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>첨부이미지</b-th>
            <b-td style="width: 20%">
              <a
                v-if="selectRowItem.MI_q_path_img1 != ''"
                :href="selectRowItem.MI_q_path_img1_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td style="width: 20%">
              <a
                v-if="selectRowItem.MI_q_path_img2 != ''"
                :href="selectRowItem.MI_q_path_img2_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-td>
              <a
                v-if="selectRowItem.MI_q_path_img3 != ''"
                :href="selectRowItem.MI_q_path_img3_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td></b-td>
          </b-tr>
          <b-tr>
            <b-th>등록일</b-th>
            <b-td colspan="4">{{ selectRowItem.MI_q_regdate | yyyyMMddhhmmss }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>답변내용</b-th>
            <b-td colspan="4">
              <b-form-textarea v-model="selectRowItem.MI_comment" rows="4" max-rows="6"></b-form-textarea>
            </b-td>
          </b-tr>
          <!-- <b-tr v-if="a_img1 == true || selectRowItem.MI_a_path_img1 != ''"> -->
          <b-tr>
            <b-th>답변이미지1</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile1($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td>
              <a
                v-if="selectRowItem.MI_a_path_img1 != ''"
                :href="selectRowItem.MI_a_path_img1_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td>
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg1"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지2</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile2($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td>
              <a
                v-if="selectRowItem.MI_a_path_img2 != ''"
                :href="selectRowItem.MI_a_path_img2_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td>
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg2"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>답변이미지3</b-th>
            <b-td colspan="2">
              <input type="file" accept="image/*" @change="uploadFile3($event)" id="admin-user-modal-file" />
            </b-td>
            <b-td>
              <a
                v-if="selectRowItem.MI_a_path_img3 != ''"
                :href="selectRowItem.MI_a_path_img3_1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                이미지보기
              </a>
              <div v-else>-</div></b-td
            >
            <b-td>
              <b-form-checkbox value="Y" unchecked-value="N" v-model="delCheckImg3"> 삭제 </b-form-checkbox>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>메모</b-th>
            <b-td colspan="4">
              <b-form-textarea v-model="selectRowItem.MI_memo" rows="4" max-rows="6"></b-form-textarea>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>담당자</b-th>
            <b-td colspan="2">
              <b-form-input v-if="selectRowItem.MI_manager == ''" size="sm" v-model="adminName" readonly />
              <b-form-input v-else size="sm" v-model="selectRowItem.MI_manager" readonly />
            </b-td>
            <b-th>처리일</b-th>
            <b-td colspan="2">
              <div v-if="selectRowItem.MI_a_regdate != ''">
                {{ selectRowItem.MI_a_regdate | yyyyMMddhhmmss }}
              </div>
              <div v-else>-</div>
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      adminName: this.$store.state.userStore.AM_name + "/" + this.$store.state.userStore.AM_rank,
      isLoding: false,
      memberInqList: [],
      selectRowItem: [],
      fields: [
        { key: "MI_status", label: "처리상태", thClass: "w20" },
        {
          key: "MI_name",
          label: "질문자 닉네임",
          thClass: "w20",
        },
        {
          key: "MI_q_regdate",
          label: "등록일",
          thClass: "w10",
          formatter: this.formatDate,
        },
        { key: "MI_manager", label: "담당자", thClass: "w10" },

        "상세보기",
        "삭제",
      ],
      options1: [
        { value: "처리중", text: "처리중" },
        { value: "처리완료", text: "처리완료" },
      ],

      selectedStatus: "처리중",
      keyword: "",
      perPage: 13,
      currentPage: 1,
      a_img1: false,
      a_img2: false,
      a_img3: false,
      delCheckImg1: "N",
      delCheckImg2: "N",
      delCheckImg3: "N",
      MI_a_path_img1_edit: "",
      MI_a_path_img2_edit: "",
      MI_a_path_img3_edit: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getInqList();
  },
  computed: {
    rows() {
      return this.memberInqList.length;
    },
  },
  methods: {
    uploadFile1(e) {
      this.MI_a_path_img1_edit = e.target.files[0];
    },
    uploadFile2(e) {
      this.MI_a_path_img2_edit = e.target.files[0];
    },
    uploadFile3(e) {
      this.MI_a_path_img3_edit = e.target.files[0];
    },

    async getInqList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/inq/list`);
        this.memberInqList = response.data;
        for (var i = 0; i < this.memberInqList.length; i++) {
          this.memberInqList[i].MI_q_path_img1_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img1}`;
          this.memberInqList[i].MI_q_path_img2_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img2}`;
          this.memberInqList[i].MI_q_path_img3_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img3}`;
          this.memberInqList[i].MI_a_path_img1_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img1}`;
          this.memberInqList[i].MI_a_path_img2_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img2}`;
          this.memberInqList[i].MI_a_path_img3_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img3}`;
        }
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    async getSearchInqList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/inq/search`, {
          selectedStatus: this.selectedStatus,
          keyword: this.keyword,
        });
        this.memberInqList = response.data;
        for (var i = 0; i < this.memberInqList.length; i++) {
          this.memberInqList[i].MI_q_path_img1_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img1}`;
          this.memberInqList[i].MI_q_path_img2_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img2}`;
          this.memberInqList[i].MI_q_path_img3_1 = `${this.$store.state.host}/${response.data[i].MI_q_path_img3}`;
          this.memberInqList[i].MI_a_path_img1_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img1}`;
          this.memberInqList[i].MI_a_path_img2_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img2}`;
          this.memberInqList[i].MI_a_path_img3_1 = `${this.$store.state.host}/${response.data[i].MI_a_path_img3}`;
        }
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    editBtn(item) {
      this.selectRowItem = { ...item };
      this.a_img1 = false;
      this.a_img2 = false;
      this.a_img3 = false;
    },
    modalAddImg() {
      if (this.a_img1 == false) {
        this.a_img1 = true;
      } else if (this.a_img2 == false) {
        this.a_img2 = true;
      } else {
        this.a_img3 = true;
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/user/inq/delete/${item.MI_idx}`, {
              MI_q_path_img1: item.MI_q_path_img1,
              MI_q_path_img2: item.MI_q_path_img2,
              MI_q_path_img3: item.MI_q_path_img3,
              MI_a_path_img1: item.MI_a_path_img1,
              MI_a_path_img2: item.MI_a_path_img2,
              MI_a_path_img3: item.MI_a_path_img3,
            });
            this.getSearchInqList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
    async saveInq() {
      var today = new Date();
      var year = today.getFullYear();
      var month = ("0" + (today.getMonth() + 1)).slice(-2);
      var day = ("0" + today.getDate()).slice(-2);
      var hours = ("0" + today.getHours()).slice(-2);
      var minutes = ("0" + today.getMinutes()).slice(-2);
      var seconds = ("0" + today.getSeconds()).slice(-2);
      var date = year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
      try {
        const fd = new FormData();
        fd.append("MI_status", this.selectRowItem.MI_status);
        fd.append("MI_comment", this.selectRowItem.MI_comment);
        fd.append("MI_a_path_img1", this.selectRowItem.MI_a_path_img1);
        if (this.delCheckImg1 == "N") {
          fd.append("MI_a_path_img1_edit", this.MI_a_path_img1_edit);
        } else {
          fd.append("MI_a_path_img1_edit", "");
        }
        fd.append("MI_a_path_img2", this.selectRowItem.MI_a_path_img2);
        if (this.delCheckImg2 == "N") {
          fd.append("MI_a_path_img2_edit", this.MI_a_path_img2_edit);
        } else {
          fd.append("MI_a_path_img2_edit", "");
        }
        fd.append("MI_a_path_img3", this.selectRowItem.MI_a_path_img3);
        if (this.delCheckImg3 == "N") {
          fd.append("MI_a_path_img3_edit", this.MI_a_path_img3_edit);
        } else {
          fd.append("MI_a_path_img3_edit", "");
        }
        fd.append("MI_memo", this.selectRowItem.MI_memo);
        fd.append("MI_manager", this.selectRowItem.MI_manager);
        fd.append("MI_a_regdate", date);
        fd.append("delCheckImg1", this.delCheckImg1);
        fd.append("delCheckImg2", this.delCheckImg2);
        fd.append("delCheckImg3", this.delCheckImg3);

        await this.$axios.post(`${this.$store.state.host}/admin/user/inq/edit/${this.selectRowItem.MI_idx}`, fd);
        this.$swal("Success", "수정 되었습니다.", "success");
        this.MI_a_path_img1_edit = "";
        this.MI_a_path_img2_edit = "";
        this.MI_a_path_img3_edit = "";
        this.delCheckImg1 = "N";
        this.delCheckImg2 = "N";
        this.delCheckImg3 = "N";
        this.getSearchInqList();
      } catch (err) {
        console.log(err);
        this.$toast.error("실패했습니다.");
      }
    },
  },
};
</script>

<style></style>
