<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 신고관리(판매사-채팅)</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn3" @click="goReportReviewList"> 후기신고내역 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput3">
          <div class="mr-1">
            <b-form-select v-model="selectedUsed" :options="options1"></b-form-select>
          </div>
          <div>
            <b-form-input placeholder="신고인 계정" v-model="keyword" @keyup.enter="getSearchReportList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getSearchReportList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="storeReportList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(BRS_content)="row">
              <div id="admin-store-report-reason">
                {{ row.item.BRS_content }}
              </div>
            </template>
            <template #cell(상세보기)="row">
              <b-button size="sm" variant="success" v-b-modal.modal-1 @click="editBtn(row.item)" id="board_edit_btn"> 상세보기 </b-button>
            </template>
            <template #cell(삭제)="row">
              <b-button size="sm" variant="danger" @click="deleteBtn(row.item)" id="board_edit_btn"> 삭제 </b-button>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>

      <!-- 모달 부분 -->
      <b-modal
        id="modal-1"
        size="xl"
        title="채팅 신고 상세보기"
        cancel-title="닫기"
        cancel-variant="danger"
        ok-title="저장"
        @ok="saveReport"
      >
        <b-table-simple id="store-modal-report">
          <b-tr>
            <b-th>신고인</b-th>
            <b-td>{{ selectRowItem.BRS_sm_m_id }}</b-td>
            <b-th>피신고인</b-th>
            <b-td>{{ selectRowItem.M_name }}</b-td>
            <b-th>신고일</b-th>
            <b-td>{{ selectRowItem.BRS_report_regdate | yyyyMMddhhmmss }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>신고사유</b-th>
            <b-td colspan="5">{{ selectRowItem.BRS_title }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>상세사유</b-th>
            <b-td colspan="5">{{ selectRowItem.BRS_content }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>이미지1</b-th>
            <b-td>
              <a
                v-if="selectRowItem.BRS_path_img1 != ''"
                :href="selectRowItem.BRS_path_img1"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-th>이미지2</b-th>
            <b-td>
              <a
                v-if="selectRowItem.BRS_path_img2 != ''"
                :href="selectRowItem.BRS_path_img2"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
            <b-th>이미지3</b-th>
            <b-td>
              <a
                v-if="selectRowItem.BRS_path_img3 != ''"
                :href="selectRowItem.BRS_path_img3"
                onclick="window.open(this.href, '_blank', 'width=100%'); return false;"
              >
                미리보기
              </a>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>처리상태</b-th>
            <b-td>
              <b-form-select size="sm" v-model="selectRowItem.BRS_status" :options="options1"> </b-form-select>
            </b-td>
            <b-th>담당자</b-th>
            <b-td>
              <b-form-input v-if="selectRowItem.BRS_manager != ''" size="sm" v-model="selectRowItem.BRS_manager" readonly />
              <b-form-input v-if="selectRowItem.BRS_manager == ''" size="sm" v-model="adminName" readonly />
            </b-td>
            <b-th>처리일</b-th>
            <b-td>
              <div v-if="selectRowItem.BRS_complet_regdate != null">
                {{ selectRowItem.BRS_complet_regdate | yyyyMMddhhmmss }}
              </div>
              <div v-else>-</div>
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>메모</b-th>
            <b-td colspan="5">
              <b-form-input size="sm" v-model="selectRowItem.BRS_memo" />
            </b-td>
          </b-tr>
        </b-table-simple>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      adminName: this.$store.state.userStore.AM_name + "/" + this.$store.state.userStore.AM_rank,
      isLoding: false,
      storeReportList: [],
      selectRowItem: [],
      fields: [
        { key: "BRS_sm_m_id", label: "신고인계정", thClass: "w17" },
        {
          key: "BRS_title",
          label: "신고사유",
          thClass: "w20",
        },
        {
          key: "BRS_content",
          label: "상세사유",
          thClass: "w20",
        },
        {
          key: "BRS_report_regdate",
          label: "신고일",
          thClass: "w10",
          formatter: this.formatDate,
        },
        { key: "BRS_status", label: "처리상태", thClass: "w10" },

        "상세보기",
        "삭제",
      ],
      options1: [
        { value: "처리중", text: "처리중" },
        { value: "처리완료", text: "처리완료" },
      ],

      selectedUsed: "처리중",
      keyword: "",
      perPage: 13,
      filter: null,
      currentPage: 1,
      modalMemo: "",
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getStoreReportList();
  },
  computed: {
    rows() {
      return this.storeReportList.length;
    },
  },
  methods: {
    async getStoreReportList() {
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/store/report/list/CHAT`);
        this.storeReportList = response.data;
        for (var i = 0; i < this.storeReportList.length; i++) {
          if (this.storeReportList[i].BRS_path_img1 != "") {
            this.storeReportList[i].BRS_path_img1 = `${this.$store.state.host}/${response.data[i].BRS_path_img1}`;
          }
          if (this.storeReportList[i].BRS_path_img2 != "") {
            this.storeReportList[i].BRS_path_img2 = `${this.$store.state.host}/${response.data[i].BRS_path_img2}`;
          }
          if (this.storeReportList[i].BRS_path_img3 != "") {
            this.storeReportList[i].BRS_path_img3 = `${this.$store.state.host}/${response.data[i].BRS_path_img3}`;
          }
        }
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    async getSearchReportList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/report/search`, {
          selectedUsed: this.selectedUsed,
          keyword: this.keyword,
          type: "CHAT",
        });
        this.storeReportList = response.data;
        for (var i = 0; i < this.storeReportList.length; i++) {
          if (this.storeReportList[i].BRS_path_img1 != "") {
            this.storeReportList[i].BRS_path_img1 = `${this.$store.state.host}/${response.data[i].BRS_path_img1}`;
          }
          if (this.storeReportList[i].BRS_path_img2 != "") {
            this.storeReportList[i].BRS_path_img2 = `${this.$store.state.host}/${response.data[i].BRS_path_img2}`;
          }
          if (this.storeReportList[i].BRS_path_img3 != "") {
            this.storeReportList[i].BRS_path_img3 = `${this.$store.state.host}/${response.data[i].BRS_path_img3}`;
          }
        }
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    goReportReviewList() {
      this.$router.push("/admin/store/report/review").catch(() => {});
    },
    editBtn(item) {
      this.selectRowItem = item;
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "삭제하시겠습니까?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "예",
        cancelButtonText: "아니오",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(`${this.$store.state.host}/admin/store/report/delete/${item.BRS_idx}`, {
              BRS_path_img1: item.BRS_path_img1,
              BRS_path_img2: item.BRS_path_img2,
              BRS_path_img3: item.BRS_path_img3,
            });
            this.getSearchReportList();
            this.$toast.success("삭제되었습니다.");
          } catch (err) {
            console.log(err);
            this.$toast.error("실패했습니다.");
          }
        }
      });
    },
    formatDate(value) {
      if (value == "") return "";
      var js_date = new Date(value);
      var year = js_date.getFullYear();
      var month = js_date.getMonth() + 1;
      var day = js_date.getDate();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      return year + "-" + month + "-" + day;
    },
    async saveReport() {
      var today = new Date();
      var year = today.getFullYear();
      var month = ("0" + (today.getMonth() + 1)).slice(-2);
      var day = ("0" + today.getDate()).slice(-2);
      var hours = ("0" + today.getHours()).slice(-2);
      var minutes = ("0" + today.getMinutes()).slice(-2);
      var seconds = ("0" + today.getSeconds()).slice(-2);
      var date = year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/store/report/save`, {
          BRS_idx: this.selectRowItem.BRS_idx,
          BRS_status: this.selectRowItem.BRS_status,
          BRS_manager: this.adminName,
          BRS_complet_regdate: date,
          BRS_memo: this.selectRowItem.BRS_memo,
        });

        if (response.status == 200) {
          this.$swal("Success", "저장되었습니다.", "success");
          this.getStoreReportList();
        }

        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
