<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title"> 이용정지 내역(고객)</span>
      </div>
      <div class="row ml-2 mt-1">
        <div>
          <b-button id="admin-store-member-topBtn3" @click="goUserMemberList"> 회원리스트 </b-button>
        </div>
        <div class="row" id="admin-store-member-groupInput11">
          <div>
            <b-form-input v-model="keyword" @keyup.enter="getUserSearchMemberList"></b-form-input>
          </div>
          <div>
            <b-button variant="warning" class="ml-1" @click="getUserSearchMemberList"><i class="bi bi-search"></i></b-button>
          </div>
        </div>
      </div>
      <div id="admin-dashboard-borad-top">
        <!-- 로딩 화면 -->
        <Loding v-if="isLoding == false" />

        <div v-if="isLoding == true">
          <b-table hover :items="userMemberList" :per-page="perPage" :current-page="currentPage" :fields="fields" id="admin-notice-table">
            <template #cell(M_hp)="row">
              {{ row.item.M_hp | phoneFomatter }}
            </template>
            <template #cell(M_pause_duration)="data">
              <span v-if="data.value == '무제한'">무제한</span>
              <span v-else>{{ data.value | yyyyMMdd }} 까지</span>
            </template>
            <template #cell(M_pause_memo)="row">
              <b-form-textarea id="textarea" v-model="row.item.M_pause_memo" rows="2" max-rows="6"></b-form-textarea>
            </template>
            <template #cell(수정)="row">
              <b-button size="sm" variant="primary" @click="editBtn(row.item)" id="board_edit_btn"> 수정 </b-button>
            </template>
            <template #cell(해제)="row">
              <div v-if="row.item.M_pause_duration == '무제한'">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="pauseBtn(row.item)" id="board_edit_btn"> 정지해제 </b-button>
              </div>
              <div v-if="today > row.item.M_pause_duration2">
                <b-button v-b-modal.modal-2 size="sm" variant="danger" @click="pauseBtn(row.item)" id="board_edit_btn"> 정지해제 </b-button>
              </div>
            </template>
          </b-table>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="admin-notice-table"
            id="board-pagination"
          ></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loding from "@/components/Admin_loding/Admin_loding.vue";
export default {
  components: {
    Loding: Loding,
  },
  data() {
    return {
      isLoding: false,
      userMemberList: [],
      fields: [
        { key: "M_email", label: "이메일", thClass: "w15" },
        { key: "M_name", label: "닉네임", thClass: "w15" },
        { key: "M_pause_reason", label: "정지 사유", thClass: "w20" },
        {
          key: "M_pause_duration",
          label: "정지 기간",
          thClass: "w15",
        },
        { key: "M_pause_memo", label: "메모", thClass: "w20" },
        "수정",
        "해제",
      ],
      keyword: "",
      perPage: 8,
      currentPage: 1,
      today: null,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "로그인 후 이용 바랍니다.", "warning");
        this.$router.push("/");
      }
    });
    this.isLoding = false;
    this.getUserMemberList();
    this.today = new Date();
  },
  computed: {
    rows() {
      return this.userMemberList.length;
    },
  },
  methods: {
    async getUserMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.get(`${this.$store.state.host}/admin/user/member/pauselist`);
        this.userMemberList = response.data;

        for (var i = 0; i < this.userMemberList.length; i++) {
          this.userMemberList[i].M_pause_duration2 = new Date(response.data[i].M_pause_duration);
        }

        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
    goUserMemberList() {
      this.$router.push("/admin/user/member2").catch(() => {});
    },

    async pauseBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/stoppause/${item.M_idx}`);
        if (response.status == 200) {
          this.getUserMemberList();
          this.$toast.success(item.M_name + "님" + "정상적으로 정지해제 되었습니다");
        }
      } catch (err) {
        this.$toast.error("실패했습니다.");
        console.log(err);
      }
    },
    async editBtn(item) {
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/editpause/${item.M_idx}`, {
          M_pause_memo: item.M_pause_memo,
        });
        if (response.status == 200) {
          this.getUserMemberList();
          this.$swal("Success", "수정 되었습니다.", "success");
        }
      } catch (err) {
        this.$toast.error("실패했습니다.");
        console.log(err);
      }
    },
    async getUserSearchMemberList() {
      this.isLoding = false;
      try {
        const response = await this.$axios.post(`${this.$store.state.host}/admin/user/member/searchpause`, { keyword: this.keyword });
        this.userMemberList = response.data;
        this.isLoding = true;
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

<style></style>
